.site-footer-lower {
    background: $color-masthead-dark;
    padding: $space-half 0;
    ul {
        padding: 0;
        margin: 0;

        li {
            margin-bottom: $space-half;

        	@media (min-width: $screen-xs-min) {
        		display: inline-block;
        		margin: 0;
        	}

            font-size: $font-size-small;
            color: #b7b7b7;
            a {
                color: $color-white;
                padding: 0;
                @media (min-width: $screen-xs-min) {
	        		padding: $space-half;
	        	}
                &:hover,
                &:focus {
                	background: none;
                	text-decoration: underline;
                }
            }
        }
	}

	body.business & {
        background: $color-masthead;
        padding-top: 52px;
        position: relative;

		&::before,
		&::after {
			display: block;
            position: absolute;
			height: 108px;
			top: -35px;
			background-image: url(../img/bs-swoosh.svg); 
			background-size: 826.168px 107.421px;

			@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
				background-image: url(../img/bs-swoosh.png);
			}

			@media (min-width: $screen-xs-min) {
				top: -35px;
			}

			@media (min-width: 838px) {
				top: -32px;
			}
		}

        &::before {
            content: "";
            left: 0;
            right: 0;
			background-position: left -2px center;

			@media (min-width: 696px) {
				right: 300px;
				max-width: 650px;
			}
		}

		&::after {
			content: none;
			right: 0;
			width: 500px;
			background-position: left -2px center;

			@media (min-width: 696px) {
				content: "";
			}

			@media (min-width: 860px) {
				width: 800px;
				background-position: right -2px center;
			}
		}

		@media (min-width: $screen-sm) {
			.nav {
				margin-top: 1em;
			}
		}

		.ie9 & {
			&::before,
			&::after {
				content: none;
			}
		}
	}

	.business-section--ug & {

		&::before,
		&::after {
			background-image: url(../img/bs-swoosh-ug.svg); 

			@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
				background-image: url(../img/bs-swoosh-ug.png);
			}
		}
	}

