// All accordion styling is js dependent. If no js use default styling
.js {

  .uol-accordions {
      border-top: 1px solid $color-border--light;
      margin-bottom: 8px;

      @include media(">=tablet") {
        margin-bottom: 16px;
      }
  }

  .uol-accordion {
    position: relative;
    padding: 8px;
    border-bottom: 1px solid $color-border--light;
    opacity: 0;
    transition: opacity 0.3s ease;

    @include media(">=tablet") {
      padding: 16px;
    }
  }

    .uol-accordion__title {
      text-align: left;
      font-size: 14px;
      font-weight: 400;
      color: $color-font--dark;
      line-height: 1.4;
      margin: -8px;

      @include media(">=tablet") {
        font-size: 18px;
        margin: -16px;
      }

      @include media(">=desktop") {
        font-size: 20px;
      }
    }

    .uol-accordion__btn {
      width: 100%;
      text-align: inherit;
      background: none;
      border: none;
      padding: 8px;

      @include media(">=tablet") {
        padding: 16px;
      }

      &::before {
          position: absolute;
          right: 8px;

          @include media(">=tablet") {
            right: 16px;
          }

          @include media(">=desktop") {
            top: 19px;
          }
      }
    }

    .uol-accordion__content {
      padding-top: 8px;
      padding-bottom: 8px;
      margin-bottom: 0;
      position: relative;
      font-size: 14px;
      // display: none;
      // max-height: 0;
      transition: max-height 0.5s ease;
      overflow-y: hidden;

      *:last-child {
        margin-bottom: 0;
      }

      @include media(">=tablet") {
        font-size: 16px;
        padding-top: 16px;
      }

      @include media(">=desktop") {
        font-size: 18px;
      }
    }

      .uol-accordion__content--expanded {

      }
}
