/*
 *  $LISTS - for more lists see '_lists.scss'
 */

 .jadu-cms,
 .cms {

   ul,
   ol,
   dl {
      margin: 0 0 20px 20px;
      padding: 0;
           ul {
               margin-bottom: 0;
           }
   }

   ul ul {
     margin-bottom: 0;
   }

   li {
     line-height: 120%;
       padding: 0;
       margin: 10px 0;
       font-size: 14px;

     @media (min-width: $screen-xs-min) {
       font-size: 16px;
       line-height: 150%;
       margin: 10px 0;
     }

     @media (min-width: $screen-md-min) {
       font-size: 1em;
       line-height: 170%;
       margin: 2px 0;
     }
   }

   dl {
     dt,
     dd {
       margin-left: $space;
     }
   }

   .list-no-style {
     list-style: none;
     padding: 0;
     margin: 0;

     li {
       list-style: none;
     }
   }

   blockquote {
     @include cms-margin();
   }
 }
