.widget-list-nav {

}

    .widget-list-nav__items {
        margin: 0;
        padding: 0;
        list-style: none;

        @media (min-width: $screen-sm) {
            columns: 2;
            column-gap: 16px;
        }

        @media (min-width: $screen-md) {
            columns: 3;
        }
    }

    .widget-list-nav__item {
        padding: 0;
    }

    .widget-list-nav__link {
        display: inline-block;
        font-size: $font-size-5;
        text-transform: none;
        color: $color-font-dark;
        transition: color 0.3s ease;
        position: relative;
        padding-left: 1.2em;
        margin-bottom: 10px;

        &:hover,
        &:focus {
            color: $color-brand-2;
            outline-offset: -5px;
        }

        &::before {
            @include ds-icon;
            @include ds-icon-chevron-right();

            position: absolute;
            left: 0;
            top: 0.3em;
            color: $color-brand-2;
        }
    }
