.uol-button {
    display: inline-block;
    line-height: 1em;
    font-size: 18px;
    padding: 10px 20px;
    border-radius: 2px;
    color: $color-white;
    border: 1px solid $color-brand-1;
    background-color: $color-brand-1;
    transition: background 0.3s ease;

    &:focus {
        border-radius: inherit;
        color: $color-white;
        text-decoration: none;
    }

    &:hover {
        border-color: $color-uol-button-bg--hover;
        background-color: $color-uol-button-bg--hover;
        color: $color-white;
        text-decoration: none;
    }

    &:disabled {
        border-color: $color-uol-button-bg--disabled;
        background-color: $color-uol-button-bg--disabled;
    }
}

    .uol-button--dark {
        border-color: $color-uol-button-bg-dark;
        background-color: $color-uol-button-bg-dark;

        &:hover {
            border-color: $color-uol-button-bg-dark--hover;
            background-color: $color-uol-button-bg-dark--hover;
        }

        &:disabled {
            border-color: $color-uol-button-bg-dark--disabled;
            background-color: $color-uol-button-bg-dark--disabled;
        }
    }

    .uol-button--brand-2 {
        border-color: $color-brand-2-btn;
        background-color: $color-brand-2-btn;

        &:hover {
            border-color: $color-uol-button-bg-dark--hover;
            background-color: $color-uol-button-bg-dark--hover;
        }

        &:disabled {
            border-color: $color-uol-button-bg-dark--disabled;
            background-color: $color-uol-button-bg-dark--disabled;
        }
    }

    .uol-button--transparent[type=button] {
        background: transparent;
        color: $color-font-lighter;
        padding: 7px 12px;
        border: none;

        &:focus,
        &:hover {
            background: transparent;
            color: $color-font-dark;
        }

        &:disabled {
            opacity: 0;
        }
    }

    .uol-button--icon {
        padding-bottom: 8px;

        .ds-icon {
            margin-left: 1.5em;
        }
    }

