/**
 * Typography
 */

/**
 * $GENERAL
 * $HEADINGS
 * $PARAGRAPHS
 * $LINKS
 * $BLOCKQUOTES
 * $LISTS
 * $SPANS
 * $MODIFIERS
 */

@import "typography/headings";
@import "typography/paragraphs";
@import "typography/links";
@import "typography/blockquotes";
@import "typography/list";

@if $use-freight-font == true { // if we're using freight (set in _vars.scss)

  html {
      font-size: 55%;
      @media (min-width: $screen-md-min) {
          font-size: 62.5%;
      }
  }

  /*
  * $GENERAL
  */

  * { // Change number to alternative glyphs https://helpx.adobe.com/typekit/using/open-type-syntax.html#lnum
    font-variant-numeric: lining-nums;
      font-feature-settings: "lnum";
  }

  body {
    color: $color-font;
    font-weight: $font-weight-book;
    font-size: 1.5rem;
    text-rendering: optimizeLegibility;

    @media (min-width: $screen-sm-min) {
            font-size: 1.6rem;
    }
  }

  .jadu-cms, //Content in the WYSIWYG
  .cms {
    @media (min-width: $screen-xs-min) {
      font-size: 18px;
      line-height: 180%;
    }
  }

  //dark background cms used on dark tabs in accommodation
  .cms-dark {
    * {
      color: white;
    }
  }

  //silent class used for all small uppercase text
  //divider, page-contents, page-footer, expander, sidebar, tabs, lists

  %all-caps {
    font-family: $font-family-sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 110%;

    @media (min-width: $screen-xs-min) {
      letter-spacing: 0.05em;
      line-height: 140%;
      font-size: 15px;
    }

    @media (min-width: $screen-sm-min) {
      font-size: 15px;
    }
  }

  .all-caps { // Used to create all caps
    @extend %all-caps;
  }

  /*
  *  $SPANS
  */

  .name-divider {
    position: relative;
    &::after {
      content: " /// ";
    }
  }

  .hightlight { //used to highlight keyword in search results
    background: #e6d4bf;
  }

  /*
  *  $MODIFIERS
  */

  .font-family-big {
    font-family: $font-family-big;
  }

  .font-family-sans-serif {
    font-family: $font-family-sans-serif;
  }

  .font-family-serif {
    font-family: $font-family-serif;
    }

    .font-size-sm {
        font-size: 14px;
    }

  .color-white {
    color: white !important;
    * {
      color: white !important;
    }
    }

    .color-font {
    color: $color-font !important;
    * {
      color: $color-font !important;
    }
  }

  .color-brand-2 {
    color: $color-brand-2 !important;
    > * {
      color: $color-brand-2 !important;
    }
  }
} //freight conditional

@if $use-google-font == true {

  h1,
  .h1 {
    font-size: 30px;
    @media (min-width: $screen-xs-min) {
      font-size: 40px;
    }
    @media (min-width: $screen-sm-min) {
      font-size: 45px;
    }
    @media (min-width: $screen-md-min) {
      font-size: 48px;
    }
  }

  h2,
  .h2 {
    font-size: 22px;
    @media (min-width: $screen-xs-min) {
      font-size: 26px;
    }
    @media (min-width: $screen-sm-min) {
      font-size: 32px;
    }
  }

  h3,
  .h3 {
    font-size: 20px;
    @media (min-width: $screen-xs-min) {
      font-size: 22px;
    }
    @media (min-width: $screen-sm-min) {
      font-size: 24px;
    }
  }

  h4,
  .h4 {
    font-size: 19px;
    @media (min-width: $screen-xs-min) {
      font-size: 20px;
    }
    @media (min-width: $screen-sm-min) {
      font-size: 22px;
    }
  }

  h5,
  .h5 {
    font-size: 19px;
    @media (min-width: $screen-xs-min) {
      font-size: 19px;
    }
    @media (min-width: $screen-sm-min) {
      font-size: 20px;
    }
  }

  h6,
  .h6 {
    font-size: 19px;
    @media (min-width: $screen-xs-min) {
      font-size: 19px;
    }
    @media (min-width: $screen-sm-min) {
      font-size: 20px;
    }
  }

  .lead { //Lead text
    line-height: 140%;
    font-size: 16px;
    margin-bottom: $space-sm;
    font-weight: $font-weight-book;

    @media (min-width: $screen-xs-min) {
      font-size: 17px;
      margin-bottom: $space;
    }
    @media (min-width: $screen-sm-min) {
      line-height: 150%;
      font-size: 18px;
    }
    @media (min-width: $screen-md-min) {
      line-height: 150%;
      font-size: 18px;
    }
  }
}

//dark background cms used on dark tabs in accommodation
.cms-dark {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  a {
    color: #fff;
  }
  a {
    color: $color-white;
    border-bottom: 1px solid $color-brand-2;

    &:hover {
      text-decoration: none;
      color: $color-brand-2;
    }
  }
}
