.banner {
    position: relative;
    overflow: auto;
    background-color: $color-masthead;
}

    .banner__image {
        padding: 0 0 50%;
        background: no-repeat 50% / cover;

        @media (min-width: 1024px) {

            .banner--type-1 & {
                padding-bottom: 33%;
                width: calc(100% / 3 * 2);
                float: right;
            }

            .banner--type-2 & {
                padding-bottom: 40%;
            }

        }
    }

    .banner__content {
        padding: 16px;
        background: $color-masthead;
        color: #fff;

        @media (min-width: 1024px) {
            position: absolute;

            .banner--type-1 & {
                left: 0;
                top: 0;
                bottom: 0;
                width: calc(100% / 3);
                float: left;
            }

            .banner--type-2 & {
                background: linear-gradient(0deg, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%);
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }
    }

        .banner__content__inner {

            @media (min-width: 1024px) {
                position: absolute;
                left: 22px;
                right: 22px;

                .banner--type-1 & {
                    bottom: 22px;
                }

                .banner--type-2 & {
                    bottom: 0;
                    padding: 6% 25% 92px;
                    text-align: center;

                    &::before {
                        content: 'Scroll';
                        speak: none;
                        text-transform: uppercase;
                        position: absolute;
                        bottom: 48px;
                        left: 50%;
                        transform: translateX(-50%);
                    }

                    &::after {
                        @include ds-icon();
                        content: "\EA6D";
                        speak: none;
                        position: absolute;
                        bottom: 10px;
                        left: calc(50% - 5px);
                        font-size: 42px;
                        transform: translateX(-50%) rotate(90deg) rotateY(35deg);
                        width: 48px;
                        height: 48px;
                        animation: pulseScroll 2s ease 2s 2;
                    }
                }
            }
        }

    .banner__title {
        font-size: 20px;
        margin-bottom: 8px;

        @media (min-width: $screen-sm) {
            font-family: $font-family-big;
            font-size: 26px;
        }

        @media (min-width: 1024px) {
            font-size: 32px;

            .banner--type-2 & {
                font-size: 40px;
                position: relative;
                display: table;
                margin: 0 auto 16px;
                width: auto;

                &::before {
                    content: "";
                    position: absolute;
                    width: 70%;
                    height: 1px;
                    bottom: -8px;
                    left: 15%;
                    background-color: $color-brand-2;
                }
            }
        }

        @media (min-width: $screen-lg) {
            font-size: 38px;

            .banner--type-2 & {
                font-size: 40px;
            }
        }
    }

    .banner__text {
        font-size: 16px;

        @media (min-width: $screen-sm) {
            font-size: 18px;
        }

        @media (min-width: 1024px) {
            font-family: $font-family-big;
            font-size: 22px;
            padding-bottom: 0.5em;

            .banner--type-1 & {
                margin-bottom: 0.8em;
                border-bottom: 1px solid $color-brand-2;
            }
        }
    }

    .banner__cta {
        font-size: 13px;
        color: #fff;

        .banner--type-1 & {
            @extend %more;

            color: #fff;
        }

        @media (min-width: 1024px) {

            .banner--type-1 & {
                font-size: 18px;
            }

            .banner--type-2 & {
                font-size: 16px;
                text-transform: uppercase;
                border: 1px solid #fff;
                background: rgba(33,33,33,.7);
                padding: 12px 48px;
                display: inline-block;
            }
        }

        &:hover,
        &:focus {
            color: inherit;
        }

    }


@keyframes pulseScroll {
    0%   {bottom: 10px;}
    50%  {bottom: 5px;}
    100% {bottom: 10px;}
}