h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-weight: $font-weight-book;
  margin-top: 0;
  margin-bottom: $space-sm;

  @media (min-width: $screen-sm-min) {
    margin-bottom: $space;
  }

  a {
    color: $color-brand-1;

    &:hover {
      color: $color-brand-2;
      text-decoration: underline;
    }
  }

  .no-touch & {
        a {
            text-decoration: none;
        }
    }
}

h1,
.h1 {
  font-size: 30px;
  @media (min-width: $screen-xs-min) {
    font-size: 40px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 45px;
  }
  @media (min-width: $screen-md-min) {
    font-size: 48px;
  }
}

h2,
.h2 {
  font-size: 22px;
  @media (min-width: $screen-xs-min) {
    font-size: 26px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 28px;
  }
}

h3,
.h3 {
  font-size: 20px;
  @media (min-width: $screen-xs-min) {
    font-size: 22px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 24px;
  }
}

h4,
.h4 {
  font-size: 19px;
  @media (min-width: $screen-xs-min) {
    font-size: 20px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 22px;
  }
}

h5,
.h5 {
  font-size: 19px;
  @media (min-width: $screen-xs-min) {
    font-size: 19px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 20px;
  }
}

h6,
.h6 {
  font-size: 19px;
  @media (min-width: $screen-xs-min) {
    font-size: 19px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 20px;
  }
}

.jadu-cms,
.cms {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: 130%;
    @media (min-width: $screen-xs-min) {
      line-height: 125%;
    }
  }
}

.cms-heading-serif {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: $font-family-big;
  }
}

.cms-dark {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: #fff;
  }
}

/**
 * Size modifiers
 */

.h1-sm,
.h2-lg {
  font-size: 26px;
  @media (min-width: $screen-xs-min) {
    font-size: 32px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 35px;
  }
  @media (min-width: $screen-md-min) {
    font-size: 40px;
  }
  }

  .h2-sm,
.h3-lg {
  font-size: 20px;
  @media (min-width: $screen-xs-min) {
    font-size: 26px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 29px;
  }
  @media (min-width: $screen-md-min) {
    font-size: 34px;
  }
}

/**
 * Heading underline - Centred heading with underline
 */

.heading-underline { // use text-center on parent to center as inlineblock is preventing it
  position: relative;
  display: table;
   text-align: center;
   margin: 0 auto;
   margin-top: $space-half;
  margin-bottom: $space;
  font-family: $font-family-big;

  @media (min-width: $screen-xs-min) {
    margin-bottom: $space*2;
  }

  &::before {
    content: "";
    position: absolute;
    width: 70%;
    height: 1px;
    bottom: -8px;
    left: 15%;
    background-color: $color-brand-2;
  }
}

/**
 * Heading related - mostly used in cards for the date or the category
 */

%heading-related {
  @extend %all-caps;

  font-family: $font-family-sans-serif;
  margin: 0;
  color: $color-font-light;
  margin-bottom: $space-half;
  line-height: 140%;

  @media (min-width: $screen-xs-min) {
    line-height: 160%;
  }

  a {
    color: $color-brand-2;
    text-decoration: none;
    border: 0;
    &:hover {
      text-decoration: underline;
    }
  }

  a.active {
    color: $color-brand-1;
    text-decoration: underline;
  }
}

.heading-related {
  @extend %heading-related;
}

.heading-related-small {
  @extend %heading-related;

  font-size: 12px;

  @media (min-width: $screen-xs-min) {
    font-size: 12px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 13px;
  }
}

/**
 * Heading Links - Used mostly in cards for headings with a link in them
 */

%heading-link {
  font-size: 20px;
    margin: 0 0 $space-half 0;
    font-family: $font-family-sans-serif;
    line-height: 120%; // look at flags text wrap

  @media (min-width: $screen-sm-min) {
    font-size: 22px;
  }

  a {
      color: $color-font;
      @extend %hover-border;
  }
}

.heading-link { // Sans heading for cards
    @extend %heading-link;
}

.heading-link-lg { // Sans heading for cards
    @extend %heading-link;

    font-size: 26px;
}

.heading-link-alt { // Big pro heading for cards
  font-family: $font-family-big;
  font-size: 18px;
  line-height: 130%;
  margin-top: 0;

  @media (min-width: $screen-xs-min) {
    font-size: 20px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 20px;
  }

  @media (min-width: $screen-md-min) {
    font-size: 23px;
  }

  a {
    @extend %hover-border;

    color: $color-font;
  }
  }

  .heading-link-alt-sm {
      @extend .heading-link-alt;
      @media (min-width: $screen-md-min) {
    font-size: 20px;
  }
  }

.heading-more, //depracated
.heading-link-more { //just like a heading link but with an arrow
  @extend .heading-link;

  display: inline-block;
  &::after {
    @include ds-icon();
    @include ds-icon-chevron-right();

    margin-left: 5px;
    position: relative;
    color: $color-brand-2;
  }
}

.heading-more-sm, //depracated
.heading-link-more-sm { //
  @extend .heading-link;

  font-size: 16px;
    margin: 0 0 $space-half 0;
    font-family: $font-family-sans-serif;

  @media (min-width: $screen-xs-min) {
    font-size: 16px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 17px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 18px;
  }

  a {
      color: $color-font;
      border: 0;
      &:hover {
          text-decoration: underline;
      }
  }

  &::after {
    @include ds-icon();
    @include ds-icon-chevron-right();

    margin-left: 5px;
    position: relative;
    color: $color-brand-2;
  }
}

@media all and (-ms-high-contrast: none) { //dealing with ie arrow wrap
    /* IE10 */
    .heading-more::after,
    .heading-link-more::after,
    .heading-more-sm::after,
    .heading-link-more-sm::after {
      content: "" !important;
      font-size: 14px;
      margin-left: 7px;
      top: 0;
    }
  }

  /* Headind dependant on font */

  .heading-serif {
    font-family: $font-family-serif;
  }
