/**
 * $SITESEARCH
 */

// See .state-site-search-active applied to _layout.scss

.site-search {	
	background: white;

	@media (max-width: $screen-xs-min) {
		//display: none !important; //overide .collapse
	}
}

	.site-search-inner {
		@extend .clearfix;

		padding: 5px 0;
	}

		.site-search-input,
		.site-search-select,
		.site-search-submit {
			float: left;
		}

		input.site-search-input {
			border: 1px solid $color-border;
			padding: 5px;								
			width: 54%;		
			margin: 0 1% 0 0;

			@media (max-width: $screen-xs-min) {
				width: 100%;
				margin: 0 0 5px 0;
			}
		}

		.lt-ie9 input.site-search-input {
			height: 40px;
		}

		select.site-search-select {
			padding: 5px 25px 5px 10px;
			width: 29%;	
			margin: 0 1% 0 0;
			min-height: 40px;

			@media (max-width: $screen-xs-min) {
				width: 75%;
				margin-bottom: 0;
			}
		}

		.lt-ie9 select.site-search-select {
			min-height: 0;
			padding: 0;
			padding-bottom: 8px;
		}

		.no-js .site-search-select {
			display: none;
		}

		input.site-search-submit {
			border: 0;
			padding: 5px 10px;
			margin: 0;
			width: 15%;

			@media (max-width: $screen-xs-min) {
				width: 24%;
				margin-bottom: 0;
			}
		}

/**
 * The local header search button 
 * to trigger the serach dropdown
 */

 .site-search-toggle-container {
	position: relative;
	color: $color-white;
}

.site-search-toggle {
	position: absolute;
	right: 6px;
	bottom: -44px;
	display: table-cell;
	text-align: right;
	vertical-align: middle;

   @media (min-width: $screen-xs-min) {
	   font-size: 20px;
   }
}

.sm-toggle-search {
   transition: background 0.25s ease;

   @media (min-width: $screen-xs-min) {
	   display: inline-block;
	   padding: 6px 12px;
   }

   &:hover {
	   background: $color-brand-1--dark;
   }

   .no-js & {
	   display: none;
   }
}

.site-search-btn::before {
   @include ds-icon();
   @include ds-icon-search();

   font-size: 22px;
   position: relative;
   top: -2px;
}

.active .site-search-btn::before {
	@include ds-icon-close();
}