.uol-embed-youtube {
    overflow: hidden;
    position: relative;
    width:100%;

    &::after {
        content: "";
        padding-top: 56.25%;
        display: block;
    }

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}