.uol-colour-group {

}

    .uol-colour-group__title {
        border-bottom: 1px solid $color-border;
        margin-bottom: 12px !important; // Non-BEM due to Mandlebrot theme
    }

    .uol-colour-group__description {
        margin-bottom: 1em;
    }

    .uol-colour-group__colours {
        display: flex;
        flex-flow: row wrap;
        margin: -5px 0 0 -5px;
        width: calc(100% + 5px);
    }

.uol-colour {
    width: 100%;
    margin-bottom: 24px;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
    padding: 6px;

    @include media(">phone") {
        margin-left: 5px;
        margin-right: 5px;
        width: calc(50% - 10px);
    }
    @include media(">=tablet") {
        width: calc(100% / 3 - 10px);
    }

}

    .uol-colour__panel {
        padding-bottom: 42.86%;
    }

    .uol-colour__spec {
        padding: 0 10px 0 0;
        font-size: 14px;
    }

        .uol-colour__spec__name {

        }

        .uol-colour__spec__value {
            font-weight: 600;
        }

    .uol-colour__usages {
        padding-top: 12px;
    }

        .uol-colour__usages_use {
            margin-bottom: 6px;
            font-size: 14px;
        }
            .uol-colour__usages__use__eg {
                display: block;
                padding: 6px;
            }

            .uol-colour__usages__use__eg--text-dark {
                color: $color-font;
            }

            .uol-colour__usages__use__eg--text-light {
                color: $color-white;
            }

            .uol-colour__usages__use__eg--text-small {
                font-size: 12pt;
            }

            .uol-colour__usages__use__eg--text-large {
                font-size: 18pt;
            }
            .uol-colour__usages__use__eg--dark-small {
                color: $color-font;
                font-size: 12pt;
            }
            .uol-colour__usages__use__eg--dark-large {
                color: $color-font;
                font-size: 18pt;
            }
            .uol-colour__usages__use__eg--light-small {
                color: $color-white;
                font-size: 12pt;
            }
            .uol-colour__usages__use__eg--light-large {
                color: $color-white;
                font-size: 18pt;
            }
