/**
 * Heading size modifiers
 */

.h1-sm,
.h2-lg {
    font-size: 26px;
    @media (min-width: $screen-xs-min) {
        font-size: 32px;
    }
    @media (min-width: $screen-sm-min) {
        font-size: 35px;
    }
    @media (min-width: $screen-md-min) {
        font-size: 40px;
    }
}

.h2-sm,
.h3-lg {
    font-size: 20px;
    @media (min-width: $screen-xs-min) {
        font-size: 26px;
    }
    @media (min-width: $screen-sm-min) {
        font-size: 29px;
    }
    @media (min-width: $screen-md-min) {
        font-size: 34px;
    }
}

