.uol-facts-figures-container {
    padding: 16px 0;

    @include media(">=tablet") {
        padding: 32px 0;
    }

    @include media(">=desktop") {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}

.uol-facts-figures {
    // Reset to remove Bootstrap and legacy blockquote styling
    border: none;
    margin: 0;
    padding: 16px;
    text-align: center;

    @include media(">=desktop") {
        width: 33%;
        padding: 16px 32px;
    }
}

    .uol-facts-figures__headline {
        quotes: none;
        margin-bottom: 8px;
        font-family: $font-family-serif;
        font-weight: 600;
    }

        .uol-facts-figures__headline__1 {
            display: block;
            font-size: 28px;
            line-height: 36px;
            color: $color-brand-2;
            margin-bottom: 8px;

            @include media(">=tablet") {
                font-size: 44px;
                line-height: 68px;
            }
        }

            .uol-facts-figures__headline__1__emphasis {
                font-size: 36px;

                @include media(">=tablet") {
                    font-size: 68px;
                }
            }

        .uol-facts-figures__headline__2 {
            display: block;
            font-size: 16px;
            font-weight: 400;
            line-height: 1.4;

            @include media(">=tablet") {
                font-size: 18px;
            }

            @include media(">=desktop") {
                font-size: 20px;
                max-width: 300px;
                margin: 0 auto;
            }
        }

    .uol-facts-figures__footer {
        text-transform: none;
        font-size: 14px;

        @include media(">=tablet") {
            font-size: 16px;
        }
    }

    .uol-facts-figures__cite {
        font-style: normal;
    }

    .uol-facts-figures__link {
        color: $color-font;
        text-decoration: underline;
    }
