.nav-list-group__title {
    display: block;
    margin-bottom: 15px;
    padding-left: 10px;
    font-size: 14px;
    text-transform: uppercase;
    color: $color-white;
}

.nav-list-group__list {
    padding: 0;
    margin: 0 0 15px;
    line-height: 24px;
}

.nav-list-group__item {
    list-style: none;
    padding-left: 6px;
    font-size: 14px;

    &::before {
        content: "";
        display: inline-block;
        width: 3px;
        height: 1px;
        left: -5px;
        margin-bottom: 0.5ex;
        background-color: $color-brand-2;
    }
}

.nav-list-group__link {
    color: $color-off-white;

    &:hover,
    &:focus {
        color: $color-white;
    }
}
