.uolcc-container,
.uolcc-prompt-container,
.uolcc-privacy-update-container {
    z-index: 1000;

    &:focus {
        outline: none;
    }

    .uolcc-outer {
        // position: fixed;
    }

    .uolcc-prefs-header,
    .uolcc-prefs-footer {
        background-color: #0a0202;

        @media (min-width: $screen-sm-min) {
           padding: 16px 24px;
        }
    }

    .uolcc-prefs-header {
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 14px;
            right: 16px;
            width: 32px;
            height: 32px;
            background: #000 url("../img/uol-icon-tower-invert.svg") 50% 50% / cover;

            @media (min-width: $screen-sm-min) {
                right: 24px;
            }
        }
    }

    .uolcc-title {
        font-family: $ds-font-family-serif;
    }

    .uolcc-form-inner {
        @media (min-width: $screen-sm-min) {
            padding: 24px;
        }
    }

    .uolcc-prompt-inner,
    .uolcc-privacy-update-inner {
        margin: 0 auto 24px;
        max-width: 1400px;

        @media (min-width: $screen-sm-min) {
            padding-right: 30%;
        }

        .uolcc-title {
            margin-bottom: 8px;
        }

        p {
            &:last-of-type {
                margin-bottom: 24px;
            }
        }
    }

    .uolcc-cookie-detail {
        @media (min-width: $screen-sm-min) {
            margin-left: -24px;
            margin-right: -24px;
            padding-left: 24px;
            padding-right: 24px;
        }
    }

    .uolcc-cookie-detail__checkbox-container {
        @media (min-width: $screen-sm-min) {
            right: 24px;
        }
    }

    .uolcc-cookie-detail__button--open {
        margin-bottom: 16px;

        @media (min-width: $screen-sm-min) {
            right: 24px;
        }
    }

    .uolcc-cookie-detail__expand-icon {
        top: 5px;
    }

    .uolcc-cookie-detail__button--open .uolcc-cookie-detail__button-title {
        color: $color-brand-2-btn;
    }

    .uolcc-button {
        background-color: $color-brand-2-btn;
        border-color: $color-brand-2-btn;
        color: $color-white;
        margin-right: 16px;
        margin-bottom: 8px;
    }

        .uolcc-button--secondary {
            background: #2a2222;
            border-color: $color-white;
        }
}

#cookie-control-prompt,
#cookie-control-privacy-update {
    &.uolcc-prompt-container,
    &.uolcc-privacy-update-container {
        border-top: 8px solid $color-brand-2-btn;
        background-color: #0a0202;
        color: $color-white;

        a {
            color: #fff;
            text-decoration: underline;
        }
    }
}
