.uol-featured-image {
    position: relative;
    margin-bottom: 16px;
}

    .uol-featured-image__img {
        width: 100%;
        height: auto;
    }

    .uol-featured-image__caption {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background: $color-grey-dark;
        background: rgba($color-grey-dark, 0.75);
        margin: 0;
        padding: 5px 10px;
        line-height: 140%;
        color: $color-white;
        font-size: 15px;
    }