/**
 * Main Navingation
 */

/**
 * Masthead Navincon
 */

.navicon {
    position: absolute;
    bottom: 10px;
    left: 8px;
    height: 0;
    opacity: 0;

    .state-nav-desktop &,
    .state-nav-mobile & {
        height: auto;
        opacity: 1;
    }
    .state-nav-mobile & {
        bottom: auto;
        top: 7px;
        right: -78px;
        left: auto;
        transition: all 0.3s ease 0.2s;
    }
    .state-nav-desktop & {
        display: none;
    }
    button {
        text-transform: uppercase;
        color: white;
        border: 2px solid white;
        display: inline-block;
        font-size: 14px;
        background: $color-masthead;

        &::before {
            @include ds-icon();
            @include ds-icon-navicon();

            margin-right: 4px;
            font-size: 16px;
            position: relative;
            float: left;
            top: 1px;
        }
    }
}

.state-navicon-active .navicon {
    // right: -78px;
    // left: auto;
    opacity: 0;
    transition: none;
    button {
        &::before {
            @include ds-icon-close();
        }
    }
}

/**
 * Navigation
 */

.ds-nav {
    //shared styles accross breakpoints
    background: $color-nav-bar;
    position: relative;
    opacity: 0;
    height: 0;
    z-index: $zindex-navbar;
    .state-nav-desktop &,
    .state-nav-mobile & {
        opacity: 1;
        height: auto;
    }

    .no-js & {
        opacity: 1;
        height: auto;

        .ds-nav-header {
            display: none;
        }

        ul {
            margin-left: 10px;
        }

        li {
            list-style: disc;
            margin-left: 10px;
        }
        a {
            padding: 0;
        }
    }

    ul {
        padding: 0;
        margin: 0;
    }

    li {
        list-style: none;
    }

    a {
        padding: 10px 10px;
        color: $color-font;
        font-size: 15px;
        transition: background 0.25s ease;

        .js & {
            display: none;
        }

        &:hover,
        &:focus {
            text-decoration: none;
        }

        .no-js & {
            &:hover,
            &:focus {
                text-decoration: underline;
            }
        }
    }
    li.heading a {
        font-weight: $font-weight-semibold;
    }
    li.list-divider a {
        padding: 0 !important;
        border-bottom: 1px solid $color-border !important;
    }
    .ds-nav-header {
        //hidden navicon header, used to close flyout
        background: $color-masthead-light;
        button {
            color: white;
            padding: 3px 6px;
            text-transform: uppercase;
            border: 2px solid white;
            display: inline-block;
            font-size: 14px;
            margin: 6px;
            &::before {
                @include ds-icon();
                @include ds-icon-close();

                position: relative;
                float: left;
                top: 2px;
                margin-right: 4px;
            }
        }
    }

    .ds-nav-inner {
        margin: 0;
        z-index: $zindex-navbar;
    }

    .ds-nav-list {
        .ds-nav-dropdown > a {
            position: relative;
        }
        .ds-nav-dropdown > a:after {
            @include ds-icon();

            right: 5px;
            position: absolute;
            font-size: 16px;
            top: 50%;
            margin-top: -8px;
        }

        .ds-nav-dropdown > a::after {
            @include ds-icon-triangle-down();
        }

        .ds-nav-dropdown.active > a::after {
            @include ds-icon-triangle-up();
        }
    }
}

.no-js .ds-nav .ds-nav-list .ds-nav-dropdown>a:after {
    content: none;
}

/**
 * Navigation Priority
 */

/**
 * Desktop Nav
 */

//@media (min-width: $screen-xs-min) { //mobile up
.state-nav-desktop {
    .ds-nav {
        .ds-nav-header {
            display: none;
        }
        .ds-nav-list {

            > li {
                display: inline-block;
                margin-left: -3px;
            }

            > li > a {
                display: block;
                position: relative;
                text-transform: uppercase;

                &:hover,
                &:focus {
                    text-decoration: none;
                    background: $color-module-darker;
                }
            }

            > li.ds-nav-dropdown {
                position: static;

                > a:after {
                    position: relative;
                    font-size: 16px;
                    top: 2px;
                    left: 3px;
                }

                > ul {
                    z-index: $zindex-dropdown;
                    width: 100%;
                    left: 0;
                    transform: rotateX(-90deg);
                    transform-origin: 0 0;
                    background: #fff;
                    position: absolute;
                    transition: all 0 ease;
                    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
                    border-top: 0;
                    overflow: hidden;

                    @media (min-width: $screen-xs-min) {
                        column-count: 2;
                        padding: 12px 24px 24px;

                        li {
                            width: 100%;
                            break-inside: avoid;
                            opacity: 0;

                            a {
                                padding-left: 6px;
                                display: none;
                            }
                        }
                    }

                    @media (min-width: $screen-md) {
                        column-count: 3;
                    }
                }

                &.ds-nav-dropdown--items-1,
                &.ds-nav-dropdown--items-2,
                &.ds-nav-dropdown--items-3,
                &.ds-nav-dropdown--items-4,
                &.ds-nav-dropdown--items-5,
                &.ds-nav-dropdown--items-6 {

                    > ul {
                        @media (min-width: $screen-xs-min) {
                            column-count: 1;
                        }

                        @media (min-width: $screen-md) {
                            padding-right: calc((100% / 3 * 2) + 36px * 2);
                        }
                    }
                }

                &.ds-nav-dropdown--items-7,
                &.ds-nav-dropdown--items-8,
                &.ds-nav-dropdown--items-9,
                &.ds-nav-dropdown--items-10,
                &.ds-nav-dropdown--items-11,
                &.ds-nav-dropdown--items-12 {
                    > ul {
                        @media (min-width: $screen-xs-min) {
                            column-count: 2;
                        }

                        @media (min-width: $screen-md) {
                            padding-right: calc(100% / 3 + 36px * 2);
                        }
                    }
                }
            }

            > li.ds-nav-dropdown.active {
                background: $color-white;
                > a {
                    background: $color-white;
                }
                > ul {
                    left: 0;
                    width: 100%;
                    transform: rotateX(0deg);

                    li {
                        opacity: 1;
                        transition: opacity .2s ease-in-out;
                        transition-delay: .2s;

                        a {
                            display: block;
                        }
                    }

                }
            }

            ul li a {
                border-bottom: 1px solid $color-border;
                padding: $space-half 15px;

                &:hover {
                    background: $color-module;
                }
            }

            ul li.ds-nav-dropdown {
                overflow: hidden;
                z-index: 5;
                > a::after {
                    position: relative;
                    margin-left: 10px;
                    top: 3px;
                }

                ul {
                    display: none;
                    background: rgba(#19130d, 0.05);
                    border-left: 4px solid rgba($color-brand-2, 1);
                }
            }
            ul li.ds-nav-dropdown.active {

                > a {
                    background: white;
                }

                > ul {

                }
            }
        }
    }
    .ds-nav-flyout {
        .ds-nav-list {

            li.ds-nav-dropdown.active > .ds-nav-dropdown-inner {
                display: none;
                ul {
                    display: block;
                }
            }
            
            > li.ds-nav-dropdown {
                //container generated and wrapped around ul's
                > .ds-nav-dropdown-inner {
                    z-index: $zindex-navbar;
                    left: -9999px;
                    background: #fff;
                    position: absolute;
                    transition: opacity 0.25s ease;
                    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
                    border-top: 0;
                    padding: 10px 0;
                    ul {
                        border-right: 1px solid $color-border;
                        position: static;
                    }
                    li a {
                        border-bottom: 0;
                    }
                }
            }

            > li.ds-nav-dropdown.active {
                > .ds-nav-dropdown-inner {
                    left: 0;
                    width: 100%; //display: flex;
                    ul {
                        float: left;
                    }
                }
            }
        }
    }
    .ds-nav-priority {
        position: relative;

        .ds-nav-list {
            display: inline-table;
            > li {
                display: table-cell;
                white-space: nowrap;
            }
        }
    }
}

//@media (max-width: $screen-xs-min - 1) { //mobile down
.state-nav-mobile {
    &.state-navicon-active .ds-nav {
        margin-left: 0;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
    }
    .ds-nav {
        width: 100%;
        height: 100%;
        position: absolute;
        background: $color-module;
        z-index: 9;
        top: 28px;
        left: 0;
        max-width: 100%;
        margin-left: -100%;
        transition: margin 0.25s ease;
        margin-bottom: 20px;
        @media (min-width: $screen-xs-min) {
            width: 330px;
        }
        @media (min-width: $screen-sm-min) {
            //width: 25%;
        }
        .wrapper-relative {
            overflow-y: scroll;
            -webkit-overflow-scrolling: touch;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
        }
        .ds-nav-inner {
            max-width: 100%;
            list-style: none;
            margin: 0;
            padding: 0;
            width: 100%;
            transition: all 0.2s ease;
        }
        .ds-nav-list {
            overflow: auto;
            a {
                display: block;
                border-bottom: 1px $color-border solid;
                border-color: rgba(black, 0.1);
            }
            ul {
                border-left: 4px solid $color-brand-2;
                background: rgba(black, 0.1);
            }
            li.ds-nav-dropdown > ul,
            li.ds-nav-dropdown > .ds-nav-dropdown-inner > ul {
                // display: none;
                transform-origin: 0 0;
                visibility: hidden;
                transform: rotateX(-90deg);
                height: 0;
                transition: all ease .25s;

                li {
                    opacity: 0;
                }

                &.active {
                    transform: rotateX(0deg);
                    height: auto;
                    visibility: visible;
                    transition: all ease .25s;

                    li {
                        opacity: 1;
                    }
                }
            }
            li.ds-nav-dropdown.active > ul,
            li.ds-nav-dropdown.active > .ds-nav-dropdown-inner > ul {

            }
        }
    }
    .ds-nav-flyout {
        .ds-nav-list {
            li.ds-nav-dropdown > .ds-nav-dropdown-inner {
                display: none;
                ul {
                }
            }
            li.ds-nav-dropdown.active > .ds-nav-dropdown-inner {
                ul {
                    display: block;
                }
            }
        }
    }
}

/**
 * CTA nav
 */

.ds-nav {
    .ds-nav-list-cta > li > a:after {
        @include ds-icon();
        @include ds-icon-chevron-right();

        margin-left: 5px;
        position: relative;
        top: 2px;
        color: $color-brand-2;
    }
}

.state-nav-desktop {
    //mobile up
    .ds-nav {

        .ds-nav-list-cta {
            float: right;

            > li > a {
                background: $color-masthead;
                color: white;
                padding: 10px 20px;

                &:hover {
                    background: $color-masthead-dark;
                }

                &::after {
                    display: none;
                }
            }
        }
    }
}

.state-nav-mobile {
    //mobile down
    .ds-nav {
        .ds-nav-list-cta {
            border-top: 3px solid $color-brand-2;
            > li > a {
                background: $color-module-dark;
                &::after {
                    margin-left: 5px;
                    position: relative;
                    top: 2px;
                    color: $color-brand-2;
                    float: right;
                }
            }
        }
    }
}

/**
 * CTA nav
 */

.ds-nav {
    .ds-nav-list-cta > li > a::after {
        @extend .ds-icon;
        @extend .ds-icon-chevron-right::before;

        margin-left: 5px;
        position: relative;
        top: 2px;
        color: $color-brand-2;
    }
}

@media (min-width: $screen-xs-min) {
    //mobile up

    .ds-nav {
        .ds-nav-list-cta {
            float: right;

            > li > a {
                background: $color-masthead;
                color: white;
                padding: 10px 20px;
                &:hover {
                    background: $color-masthead-dark;
                }
                &:after {
                    display: none;
                }
            }
        }
    }
}

@media (max-width: $screen-xs-min - 1) {
    //mobile down

    .ds-nav {
        .ds-nav-list-cta {
            border-top: 3px solid $color-brand-2;
            > li > a {
                background: $color-module-dark;

                &::after {
                    margin-left: 5px;
                    position: relative;
                    top: 2px;
                    color: $color-brand-2;
                    float: right;
                }
            }
        }
    }
}
