.page-heading {
  text-align: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

  .page-heading--icon {
    position: relative;
    padding-top: 90px;

    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: $color-brand-2;
      position: absolute;
      top: 40px;
      left: 0;
      right: 0;
    }

    &::after {
      content: "";
      display: block;
      width: 100px;
      height: 100px;
      background: #fff;
      border: 20px solid #fff;
      border-radius: 50%;
      position: absolute;
      top: -10px;
      left: 50%;
      transform: translateX(-50%);
    }
  }

    .page-heading--icon--uol {
      &::after {
          background: #fff url("../img/uol-tower.png") 50% 50% / cover;
          background-position: center;
      }
    }

  .page-heading__title {
      @extend .heading-underline;

      order: 2;
  }

  .page-heading__meta {
    width: 100%;
    order: 1;
  }

    .page-heading__meta__term {
      @extend .sr-only;
    }

    .page-heading__meta__data {
      display: inline;
      margin: 0;
      white-space: nowrap;
      text-transform: uppercase;
      font-size: 16px;

      &::after {
        content: "";
        display: inline-block;
        position: relative;
        top: 0.2em;
        width: 1em;
        height: 1em;
        background: url("../img/divider-slash-triple.svg") 50% / cover;
        margin: 0 0.3em 0 0.5em;
      }

      &:last-of-type::after {
        content: none;
      }
    }

      .page-heading__meta__data__link {
          @extend .link-color-2;
      }
