//Search results list

.search-results-list {
    clear: both;

    @media (min-width: $screen-md) {
        float: right;
        padding-top: 20px;
    }
}

    .search-results-item {
        position: relative;
        border-bottom: 1px solid #c4c4c4;
        margin-bottom: 16px;
        padding-bottom: 16px;
    }

        .search-results-item__img {
            padding-bottom: 16px;
            @media (min-width: $screen-sm) {
                float: left;
                width: 33.33%;
                padding: 0 16px 0 0;
            }
        }

        .search-results-item__header {
            display: flex;
            flex-direction: column;
        }

        .search-results-item__title {
            @extend .heading-link;

            order: 2;
        }

        .search-results-item__meta {
            order: 1;
            margin-bottom: 8px;
        }

            .search-results-item__meta__label {
                @extend .hide-accessible;
            }

            .search-results-item__meta__data {
                font-size: 15px;
                font-weight: 400;
                text-transform: uppercase;
                display: inline-block;

                &::after {
                    content: "";
                    display: inline-block;
                    position: relative;
                    top: .2em;
                    width: 1px;
                    height: 1em;
                    background: $color-font;
                    margin: 0 0.3em 0 0.5em;
                }

                &:last-of-type::after {
                    content: none;
                }
            }

        .search-results-item__description {
            font-size: 16px;
            margin-bottom: 0;
        }

        .search-results-item__meta {
            color: #757575;
        }
