// Required Glide.js Core Stylesheet
@import "./node_modules/@glidejs/glide/src/assets/sass/glide.core";

// Slides container
.slider {
    position: relative;
}

    .slider--bullets {
        margin-bottom: 2em;
    }

    .slider__track {
        overflow: hidden;
    }
    .slider__slides {
        @extend .glide__slides;
    }

    // Slide
    .slider__slide {
        white-space: normal;

        .js & {
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.3s ease;
        }

        .js .slider--dragging &,
        .js .slider--moving & {
            opacity: 1;
            visibility: visible;

            @media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
                &[aria-hidden="true"] {
                    opacity: 0;
                }
            }
        }
    }

        .slider__slide--active {

            .js & {
                opacity: 1;
                visibility: visible;
            }
        }

    // Slider arrows
    .slider__arrow {
        display: block;
        position: absolute;
        top: 5px;
        text-indent: -9999px;
        width: 33px;
        height: 30px;
        background-color: #212121;
        border: 0 solid $color-brand-2;
        // transition: opacity 0.2s ease;

        @media (min-width: $screen-xs) {
            width: 43px;
            height: 40px;
        }

        @media (min-width: 1500px) {
            top: 50%;
            transform: translateY(-50%);
            width: 55px;
            height: 50px;
        }

        &::before {
            @include ds-icon();

            display: block;
            position: absolute;
            margin-left: 1px;
            text-indent: 0;
            text-align: center;
            top: 50%;
            transform: translateY(-50%);
            font-size: 16px;
            color: #fff;

            @media (min-width: $screen-xs) {
                font-size: 24px;
            }

            @media (min-width: 1500px) {
                font-size: 32px;
            }
        }

        &:disabled {
            opacity: 0.5;
        }
    }

        .slider__arrow--left {
            right: 40px;
            border-right-width: 3px;

            @media (min-width: $screen-xs) {
                right: 55px;
            }

            @media (min-width: 1500px) {
                border-right-width: 5px;
                right: auto;
                left: -50px;
            }

            &::before {
                content: "\EA6C";
            }
        }

        .slider__arrow--right {
            right: 5px;
            border-left-width: 3px;

            @media (min-width: 1500px) {
                border-left-width: 5px;
                right: -50px;
            }

            &::before {
                content: "\EA6D";
            }
        }

    // Slider bullets
    .slider__bullets {
        position: absolute;
        left: 50%;
        bottom: -40px;
        transform: translateX(-50%);
    }

        .slider__bullet {
            text-indent: -9999px;
            display: inline-block;
            margin: 0 4px;
            padding: 0;
            height: 12px;
            width: 12px;
            background: $color-masthead;
            border-radius: 50%;
        }

            .slider__bullet--active {
                background: $color-brand-2;
            }

    .slider__tabs {
        display: flex;

        @media (max-width: $screen-md - 1px) {
            font-size: 0;
        }
    }

        .slider__tab {
            flex: 1;
            border: none;
            background: $color-masthead-light;
            color: #fff;
            font-size: 16px;
            padding: 1em;
            text-transform: uppercase;

            @media (max-width: $screen-md - 1px) {
                text-indent: -9999px;
                font-size: 0;
                line-height: 0;
                padding: 5px;
            }

            &:focus {
                outline: none;
                border: solid rgba(0, 103, 244, 0.7);
                border-width: 0 0 2px;
                box-sizing: border-box;
                padding: 5px 5px 3px;
            }

            @media (min-width: $screen-md) {
                &:focus {
                    border-width: 2px;
                    padding: calc(1em - 2px);
                }
            }

            // Yes the use of the bullet naming is terrible but is a limitation of Glide.js
            &.slider__bullet--active {
                background: $color-brand-2;
            }
        }
