//TODO: move to mixins
@mixin cms-margin() {
  margin-bottom: $space-sm;
  @media (min-width: $screen-md-min) {
      margin-bottom: $space-half*3;
  }
}

p,
.p {
  margin-bottom: $space-sm;
  font-weight: $font-weight-book;
  font-size: 15px;
  line-height: 140%;

  @media (min-width: $screen-xs-min) {
    font-size: 16px;
    line-height: 160%;
  }

  @media (min-width: $screen-md-min) {
    margin-bottom: $space-half*3;
    font-size: 18px;
    line-height: 160%;
  }
}

.article p {
  font-family: $font-family-serif;
}

.lead { //Lead text
  @include cms-margin();

  line-height: 140%;
  font-size: 16px;
  margin-bottom: $space-sm;
  font-weight: $font-weight-book;

  @media (min-width: $screen-xs-min) {
    font-size: 17px;
    margin-bottom: $space;
  }
  @media (min-width: $screen-sm-min) {
    line-height: 150%;
    font-size: 18px;
  }
  @media (min-width: $screen-md-min) {
    line-height: 150%;
    font-size: 22px;
  }
}

.lead-alt {
  @extend .lead;

  font-family: $font-family-serif;
}

.lead-large { //Lead text
}

.jadu-cms, //deprecated
.cms {

}

.cms-lead {
  > p:first-child {
    @extend .lead;
  }
}

.cms-lead-serif {
  > p:first-child {
    @extend .lead;

    font-family: $font-family-big;
  }
}

.jadu-cms-no-lead,
.cms-no-lead {

  > p:first-child {
    margin-bottom: $space-half;
    font-weight: $font-weight-book;
    font-size: 14px;
    line-height: 150%;

    @media (min-width: $screen-xs-min) {
      font-size: 16px;
      line-height: 170%;
    }

    @media (min-width: $screen-md-min) {
      margin-bottom: $space;
      font-size: 18px;
      line-height: 170%;
    }
  }
}

.cms-dark {
  p {
    color: white;
  }
}

.cms-serif {
  font-family: $font-family-serif;
  & > * {
    font-family: $font-family-serif;
  }
}

.summary { // Used as the first paragraph of a news item
  line-height: 150%;
  font-size: 16px;
  font-family: $font-family-serif;
  @media (min-width: $screen-xs-min) {
    font-size: 20px;
  }
  @media (min-width: $screen-sm-min) {
    font-size: 22px;
  }
}

.dropcaps, //depracated
.summary-drop-caps { //Dropcap of the summary
  font-size: 40px;
  float: left;
  font-family: $font-family-big;
  line-height: 80%;
  color: $color-brand-2;
     padding: 8px 12px 0 0;

     @media (min-width: $screen-xs-min) {
    font-size: 50px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 60px;
  }
}

/**
 * Excerpt & note - used mostly in cards and news & events archive
 */

.note,
.note p,
.excerpt,
.excerpt p { //used in archive and list
    color: $color-font-light;
    line-height: 150%;
    font-size: 14px;

  @media (min-width: $screen-xs-min) {
    font-size: 15px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 16px;
    line-height: 180%;
  }

    strong {
      padding: 0 4px;
    }
}

.excerpt,
.excerpt p {
    margin-bottom: 0;
  }

  //Used in lib banner for small text
  .notice {
      font-size: 14px;
  }

  //Used in video and image cms cards
  .caption {
      font-size: 16px;
      line-height: 120%;
      padding: 6px 0;
  }

/**
 * URL's - forces a line break for a url (use with caution)
 */

.line-break,
.url {
  -ms-word-break: break-all;
    word-break: break-all;

    /* Non standard for webkit */
    word-break: break-word;
    hyphens: auto;
    margin: 0 0 10px 0;
    font-size: 14px;

  @media (min-width: $screen-xs-min) {
    font-size: 15px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 16px;
    line-height: 180%;
  }
}
