.uol-table-caption {
    font-size: 18px;
    font-weight: 600;
    color: $color-font--dark;
    margin-bottom: 0;

    @include media("<tablet") {
        padding: 16px;
        line-height: 1.333;
        background-color: $color-cream--dark;
        border-bottom: 4px solid $color-brand-1;
    }

    @include media(">=tablet") {
        padding: 16px 16px 0;
    }

}

.uol-table {
    width: 100%;
    margin-bottom: 16px;

    @include media(">=tablet") {
        margin: 16px;
        width: calc(100% - 32px);
        position: relative;
        border-radius: 4px;
        box-shadow: 0 1px 3px 2px rgba($color-black, 0.12);
    }
    // Currently unused due to Safari position: sticky bug
    // caption {
    //     font-size: 18px;
    //     font-weight: 600;
    //     color: $color-font--dark;
    // }

    thead {
        @include media(">=tablet") {
            // position: sticky;
            // top: 0;
            z-index: 2;
            border-radius: 4px 4px 0 0;
        }
    }

    th,
    td {
        vertical-align: top;
        font-size: 16px;

        @include media(">=tablet") {
            padding: 16px;
        }
    }

    th {
        @include media(">=tablet") {
            position: sticky;
            top: 0;
            padding-bottom: 20px;
            background: linear-gradient(0deg, $color-brand-1 4px, $color-cream--dark 4px, $color-cream--dark 100%);
            z-index: 2;
            transform: translateZ(0) scaleX(1.01);

            &:first-of-type {
                border-radius: 4px 0 0 0;
            }

            &:last-of-type {
                border-radius: 0 4px 0 0;
            }

            &::before {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                bottom: -4px;
                left: 0;
                right: 0;
                background: linear-gradient(0deg, rgba(#000, 0) 0, rgba(#000, 0.4) 4px, rgba(#000, 0) 4px);
                opacity: 0;
                transition: opacity 0.7s ease;
            }

            &[data-sortable="true"] {
                padding-right: 42px;
            }
        }
    }

    .thead--stuck th {
        @include media(">=tablet") {
            &::before {
                opacity: 1;
            }
        }
    }

    td {
        color: $color-font;
        font-size: 16px;

        a {
            color: $color-font;
            text-decoration: underline;

            &:hover,
            &:focus {
                color: $color-brand-1;
            }
        }
    }

    tbody {
        line-height: 1.5;

        @include media(">=tablet") {
            z-index: 1;
        }

        tr {
            transition: all 0.3s ease;

            &:hover {
                background: #efeae6;
                box-shadow: 0 3px 2px 0 rgba($color-black, 0.12);
            }
        }

        td {
            @include media(">=tablet") {
                border-bottom: 1px solid $color-border--light;
            }
        }

        tr:last-of-type td {
            border-bottom: none;
        }
    }

    .uol-table__td--type-numeric {
        @include media(">=tablet") {
            text-align: right;
        }
    }

    .uol-table__td--type-img {
        position: relative;
        height: 96px;
        margin-top: -16px;
        margin-left: -16px;
        margin-right: -16px;
        padding: 16px 16px 16px 104px;

        @include media("<tablet") {
            background: $color-cream--dark;
        }
    }

    .uol-table__td__img {
        position: absolute;
        top: 8px;
        left: 8px;
        display: block;
        width: 80px;
        height: 80px;
        background-color: $color-white;
        border-radius: 50%;
        overflow: hidden;

        @include media(">=tablet") {
            width: 64px;
            height: 64px;
            top: 16px;
        }

        &::before,
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            width: inherit;
            height: inherit;
            border-radius: inherit;
        }

        &::before {
            background: 50% / cover;
            background-image: inherit;
        }

        &::after {
            box-shadow: inset 0 2px 5px rgba($color-black, 0.16);
        }
    }

        .uol-table__td__img--profile {
            background: $color-white url("../img/avatar-missing.png");
        }

    .uol-table__sort-select {
        display: block;
        font-size: 16px;
        font-weight: 600;
        margin: 0;
        padding-left: 16px;
        background-color: $color-white;
        border-color: $color-black;

        @include media(">=tablet") {
            display: none;
        }
    }

    .uol-table__btn-sort {
        position: absolute;
        top: 16px;
        right: 16px;
        width: 25px;
        height: 24px;
        background: transparent;
        border: none;

        @include media("<tablet") {
            display: none;
        }

        &::before,
        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 10px;
            height: 10px;
            left: 8px;
            background: linear-gradient(to bottom right, rgba(#000, 0) 0%, rgba(#000, 0) 50%, $color-blacklight 50%, $color-blacklight 100%);
        }

        &::before {
            top: 5px;
            transform: rotate(225deg);
        }

        &::after {
            bottom: 5px;
            transform: rotate(45deg);
        }

        .sorted__arrow-shaft {
            display: block;
            position: absolute;
            top: 6px;
            bottom: 6px;
            left: 13px;
            width: 3px;
            background: $color-blacklight;
            opacity: 0;
            transform: translateX(-50%);
        }

        &.sorted {
            .sorted__arrow-shaft {
                opacity: 1;
            }
        }

        &.sorted--asc {

            &::before {
                opacity: 0;
            }
        }
        &.sorted--desc {

            &::after {
                opacity: 0;
            }
        }
    }
}

    // uol-table--stacked class added with javascript
    .uol-table--stacked {

        @include media("<tablet") {
            border-collapse: collapse;

            // Currently unused due to Safari position: sticky bug
            // caption {
            //     padding: 16px;
            //     line-height: 1.333;
            //     background-color: $color-cream--dark;
            //     border-bottom: 4px solid $color-brand-1;
            // }

            thead {
                position: absolute !important;
                clip: rect(1px, 1px, 1px, 1px);
                height: 1px;
                width: 1px;
                overflow: hidden;
                left: -9999px;
            }

            tbody {
                tr {
                    display: block;
                    margin-bottom: 0;
                    padding: 16px 16px 8px;
                    border-bottom: 1px solid $color-border--light;

                    &:first-of-type {
                        border-top: 1px solid $color-border--light;
                    }
                }
                td {
                    display: block;
                    margin-bottom: 16px;
                    color: $color-font--dark;
                }

                .uol-table__td--type-img {
                    position: relative;
                    background: $color-cream--dark;
                    min-height: 96px;
                    margin-top: -16px;
                    margin-left: -16px;
                    margin-right: -16px;
                    padding: 16px 16px 16px 104px;
                }

                .uol-table__td__img {
                    position: absolute;
                    top: 8px;
                    left: 8px;
                    display: block;
                    width: 80px;
                    height: 80px;
                    background-color: $color-white;
                    border-radius: 50%;
                    overflow: hidden;

                    &::before,
                    &::after {
                        content: "";
                        display: block;
                        position: absolute;
                        top: 0;
                        right: 0;
                        width: inherit;
                        height: inherit;
                        border-radius: inherit;
                    }

                    &::before {
                        background: 50% / cover; 
                        background-image: inherit;
                    }

                    &::after {
                        box-shadow: inset 0 2px 5px rgba($color-black, 0.16);
                    }

                    img {
                        width: 100%;
                        height: auto;
                    }
                }
            }
        }
    }

    .uol-table__pseudo-th {
        display: block;
        font-weight: 500;
        color: $color-font;
        margin-bottom: 4px;

        @include media(">=tablet") {
            display: none;
        }
    }
