//UoL Reds
$color-brand-1--dark:   #910000;
$color-brand-1:         #c70000;
$color-brand-1--bright: #ff4e36;
$color-brand--pastel:   #ffcccc;
$color-brandfaded:     #ffeded;

//UoL Golds
$color-brand-2--dark:   #7a5928;
$color-brand-2:         #946829;
$color-brand-2--bright: #e3982d;
$color-brand-2--pastel: #fcd59d;
$color-brand-2--faded:  #fff0db;

// UoL Blacks - Neutrals for page architecture, site masthead and footer
$color-black--dark:     #0a0202;
$color-black:           #212121;
$color-blacklight:     #6e6b6b;
$color-blackfaded:     #dedede;

// UoL Greys - Neutrals for page architecture
$color-grey--dark:      #bcb8b3;
$color-grey:            #ddd9d5;
$color-grey--light:     #f4f2f0;
$colour-greyfaded:     #fafafa;

// UoL Creams - Neutrals for page architecture
$color-cream--dark:     #e9e1d8;
$color-cream:           #f6eee5;
$color-creamlight:     #faf6f1;

// UoL Whites - Neutrals for page architecture
$color-white--dark:     #fffbf5;
$color-white:           #ffffff;

// UoL tertiary colours - Used sparingly, to provide contrast for highlighting and messaging.
$color-bluedark:       #00323d;
$color-blue:            #003a52;
$color-blue--bright:    #1a84c7;

// UoL text colours
$color-font--dark:      #0e0c0c;
$color-font:            #212529;
$color-fontlight:      #707070;

// UoL Links colours
$color-link:            #910000;
$color-link--hover:     #0e0c0c;
$color-link--visited:   #570000;
$color-link--active:    $color-link--visited;

// UoL borders colours
$color-border--light:   #c9c8c5;
$color-border:          #51504c;

// UoL Alerts colours
$color-alert--error:    #d4351c;
$color-alert--warning:  #e76f16;
$color-alert--success:  #3a9018;
$color-alert--info:     #1993bd;

// TODO: Old SCSS below - to be refactored
// Buttons
$color-brand-2-btn: $color-brand-2--dark;

$color-grey-dark:   $color-black;
$color-beige:       #f4eadf;
$color-cream:       #faf6f1;

$color-masthead:    $color-black;

//Make unique if necessary for theming
$color-profile-block:           $color-brand-1--dark;
$color-local-header:            $color-brand-1;
$color-button: 					$color-grey-dark;
$color-button-dark: 			#111000;
$color-link: 					$color-brand-1;
$color-link-hover: 				$color-brand-1--dark;

// UOL Button colours
$color-uol-button-bg: $color-brand-1;
$color-uol-button-bg--hover: #4d0202;
$color-uol-button-bg--disabled: #e1b3b3;

$color-uol-button-bg-dark: $color-grey-dark;
$color-uol-button-bg-dark--hover: $color-brand-2;
$color-uol-button-bg-dark--disabled: #bababa;

//Theme generic
$color-white: 					#fff;
$color-off-white: 				#f7f7f7;

//User Interface
$color-masthead: 				$color-black;
$color-masthead-lighter: 		#444;
$color-masthead-light: 			#272727;
$color-masthead-dark: 			#111;
$color-masthead-darker: 		#000;

//Modules
$color-module: 					#f4f2f0;
//used to be #f8f7f6
