//Borders
$color-border: 					#c9bfb5;
$color-border-light: 			#ddd5cc;
$color-border-lighter: 			#eae2da;
$color-border-dark: 			#908880;

//Masks background
$color-mask: rgba($color-masthead-dark, 0.6);
