/**
 * Heading Links - Used mostly in cards for headings with a link in them
 */

.heading-link {
    font-size: 20px;
    margin: 0 0 $space-half 0;
    font-family: $font-family-sans-serif;
    line-height: 120%; // look at flags text wrap

    @media (min-width: $screen-sm-min) {
        font-size: 22px;
    }

    a {
        @extend %hover-border;

        color: $color-font;
    }
}

.heading-link-lg { // Sans heading for cards
    @extend .heading-link;

    font-size: 26px;
}

.heading-link-alt { // Big pro heading for cards

    font-family: $font-family-big;
    font-size: 18px;
    line-height: 130%;
    margin-top: 0;

    @media (min-width: $screen-xs-min) {
        font-size: 20px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 20px;
    }

    @media (min-width: $screen-md-min) {
        font-size: 23px;
    }

    a {
        @extend %hover-border;

        color: $color-font;
    }
}

.heading-link-alt-sm {
    @extend .heading-link-alt;

    @media (min-width: $screen-md-min) {
        font-size: 20px;
    }
}

.heading-more, //depracated
.heading-link-more { //just like a heading link but with an arrow
    @extend .heading-link;

    display: inline-block;

    &::after {
        @include ds-icon();
        @include ds-icon-chevron-right();

        margin-left: 5px;
        position: relative;
        color: $color-brand-2;
    }
}

.heading-more-sm,//depracated
.heading-link-more-sm { //
    @extend .heading-link;

    font-size: 16px;
    margin: 0 0 $space-half 0;
    font-family: $font-family-sans-serif;

    @media (min-width: $screen-xs-min) {
        font-size: 16px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 17px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 18px;
    }

    a {
        color: $color-font;
        border: 0;
        &:hover {
            text-decoration: underline;
        }
    }

    &::after {
        @include ds-icon();
        @include ds-icon-chevron-right();

        margin-left: 5px;
        position: relative;
        color: $color-brand-2;
    }
}

@media all and (-ms-high-contrast: none) { //dealing with ie arrow wrap
    /* IE10 */
    .heading-more,
    .heading-link-more,
    .heading-more-sm,
    .heading-link-more-sm {

        &::after {
            content: "" !important;
            font-size: 14px;
            margin-left: 7px;
            top: 0;
        }

    }
}