.uol-widget-featured {

}

    .uol-widget-featured__content {
        position: relative;
        overflow: auto;
        padding: 8px 0;

        margin: 0 -8px;

        @media (min-width: $screen-sm) {
            display: flex;
            flex-direction: row-reverse;
        }
    }

        .uol-widget-featured__content--image-left {

            @media (min-width: $screen-sm) {
                flex-direction: row;
            }
        }

    .uol-widget-featured__text-container {
        position: relative;
        margin: 8px;
        padding-bottom: 38px;

        @media (min-width: $screen-sm) {
            width: calc(60% - 16px);
        }
    }

    .uol-widget-featured__image-container {
        margin: 8px;

        @media (min-width: $screen-sm) {
            width: calc(40% - 16px);
        }
    }

    .uol-widget-featured__figure {
        position: relative;
    }

        .uol-widget-featured__figure__caption {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 0.2em 0.5em;
            background: rgba(#000, 0.7);
            color: $color-white;
        }

        .uol-widget-featured__figure__caption--video {
            position: relative;
            background: $color-grey-dark;
            top: -5px;
        }

    .uol-widget-featured__img {
        padding-bottom: 100%;
        background: $color-brand-2 no-repeat 50% / cover;
    }

        .uol-widget-featured__img-2-1 {
            padding-bottom: 50%;
        }

    .uol-widget-featured__video {
        width: 100%;
        height: auto;
    }

    .uol-widget-featured__youtube {
        overflow: hidden;
        position: relative;
        width:100%;

        &::after {
            content: "";
            padding-top: 56.25%;
            display: block;
        }
    }

        .uol-widget-featured__youtube__iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: 0;
        }

    .uol-widget-featured__cta-container {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
    }
