
.page-contents-nav__btn {
  position: relative;
  width: 100%;
  margin: 12px 0;
  padding: 10px;
  border: none;
  background: none;
  border-top: 1px solid #c9bfb5;
  border-bottom: 1px solid #c9bfb5;
  text-align: left;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 110%;

  @media (min-width: $screen-xs) {
    display: none;
  }

  &::before {
    @include ds-icon();
    @include ds-icon-chevron-down();

    position: absolute;
    top: 50%;
    right: 5px;
    font-size: 16px;
    margin-top: -8px;
  }

  &.page-contents-nav__btn-active {
    &::before {
      @extend .ds-icon-chevron-up::before;
    }
  }
}

.page-contents-nav__heading {
  margin-bottom: 16px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-bottom: 1px solid #c9bfb5;
  text-transform: uppercase;
  font-size: 15px;

  .js & {
    display: none;
  }

  @media (min-width: $screen-xs) {
    display: block;
    letter-spacing: 0.05em;
    line-height: 140%;

    .js & {
      display: block;
    }
  }
}

.page-contents-nav__inner {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #c9bfb5;

  .js & {
    display: none;
  }


  @media (min-width: $screen-xs) {
    padding-bottom: 0;    
    padding-top: 12px;

    .js & {
      display: block;
    }
  }
}

  .page-contents-nav__inner-active {
    .js & {
      display: block;
    }
  }

.page-contents-nav__list {
  padding-left: 16px;
  margin-bottom: 0;

  @media (min-width: $screen-xs) {
    width: 50%;
    float: left;
    padding-bottom: 16px;
  }
}

.page-contents-nav__item {
  margin: 0 0 5px 0;
}
