.lead { //Lead text
    // @include cms-margin();

    line-height: 140%;
    font-size: 16px;
    margin-bottom: $space-sm;
    font-weight: $font-weight-book;

    @media (min-width: $screen-xs-min) {
        font-size: 17px;
        margin-bottom: $space;
    }
    @media (min-width: $screen-sm-min) {
        line-height: 150%;
        font-size: 18px;
    }
    @media (min-width: $screen-md-min) {
        line-height: 150%;
        font-size: 22px;
    }
}

.lead-alt {
    @extend .lead;

    font-family: $font-family-serif;
}
