/*------------------------------------*\
    $island
\*------------------------------------*/

//$TEUXDEUX maybe reactor and use .well from boostrap

/**
 * island, boxed off content
 *
   <div class=island>
       I am boxed off.
   </div>

 *
 */

[class^='island'] {
    padding: $space-sm;

    @media (min-width: $screen-xs-min) {
        padding: $space;
    }
}

.island {
    display: block;
    @include clearfix();
}

//Island size

.island--lg {
    padding: $space-sm;
    @media (min-width: $screen-xs-min) {
        padding: $space-lg;
    }
}

.island--sm {
    padding: $space-half;
}

.island--m-t-b {
    margin: $space 0;
}

.island--m-t {
    margin-top: $space;
}

.island--m-b {
    margin-bottom: $space;
}

.island--module {
    @include skin-box-module-border();

    margin-bottom: $space;
}

.island--featured {
    @extend .island--module;
    @include border-left-brand-2();
}

.island--dark {
    background: $color-mask-darker;
}
