/**
 * Show and hide
 */
.hide-accessible, .search-results-item__meta__label {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  width: 1px;
  overflow: hidden;
  left: -9999px;
}

.show-accessible {
  position: static !important;
  clip: auto;
  height: auto;
  width: auto;
  overflow: visible;
  left: auto;
}

/**
 * Skip to main content btn
 */
#skip-main {
  clip: rect(0 0 0 0);
  overflow: hidden;
  position: absolute;
  height: 1px;
  width: 1px;
}

#skip-main:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  clip: auto !important;
  color: #212529;
  display: block;
  font-size: 16px;
  font-weight: bold;
  height: auto;
  left: 5px;
  padding: 16px 20px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

/**
 * BOOTSTRAP EXTENDS - Used to add additional styles to already existing boostrap styles
 */
/**
 * Collapse
 */
.no-js .collapse {
  display: block;
}

.no-js .fade {
  opacity: 1;
}

/**
 * Dropdown
 * Make dropdowns accessible
 */
.dropdown-menu {
  display: block;
  visibility: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute;
  left: -9999px;
}

.open > .dropdown-menu {
  display: block;
  visibility: visible;
  clip: auto;
  left: auto;
}

/**
 * Panels
 */
.panel-title {
  font-family: "freight-sans-pro", sans-serif;
}

/**
 * List group items
 */
.list-group-item-heading {
  font-family: "freight-sans-pro", sans-serif;
}

/* Other */
.badge {
  padding: 2px 6px 6px 6px;
}

.container-flush {
  padding: 0;
}

/* Improve blockquote */
blockquote .small,
blockquote footer,
blockquote small {
  display: inline-block;
}

.media-object {
  max-width: none !important;
}

.form-group input,
.form-group textarea,
.form-group select,
.form-group button {
  margin-bottom: 0;
}

/**
 * Columns - Used for the page sidebar layout
 */
.column-container {
  position: relative;
}

@media (min-width: 1040px) {
  .column-container .column-container-primary {
    float: left;
    width: 78%;
  }
}

@media (min-width: 1260px) {
  .column-container .column-container-primary {
    width: 75%;
  }
}

.column-container .column-container-secondary {
  position: relative;
}

@media (min-width: 1040px) {
  .column-container .column-container-secondary {
    float: left;
    width: 22%;
    padding-bottom: 40px;
    max-width: 320px;
  }
}

@media (min-width: 1260px) {
  .column-container .column-container-secondary {
    width: 25%;
  }
}

@media (min-width: 1170px) {
  .column-container .column-container-secondary {
    padding-left: 10px;
  }
}

@media (min-width: 1040px) {
  .column-container .column-container-secondary-right {
    float: right;
    width: 25%;
    border-right: 0;
  }
}

@media (min-width: 1040px) {
  .column-container-fw {
    display: table;
    width: 100%;
  }
}

@media (min-width: 1040px) {
  .column-container-fw .column-container-primary {
    width: auto;
    vertical-align: top;
    display: table-cell;
    float: none;
    max-width: 5px;
  }
}

.column-container-fw .column-container-secondary {
  padding-left: 0;
}

@media (min-width: 1040px) {
  .column-container-fw .column-container-secondary {
    float: none;
    display: table-cell;
    vertical-align: top;
    background: #f4f2f0;
    padding: 0 0 40px 0;
    width: 220px;
  }
}

@media (min-width: 1260px) {
  .column-container-fw .column-container-secondary {
    width: 300px;
  }
}

