/**
 * Skins
 */
/*

This file is a bit of a mess
many depracted skin names as we havent
been constistent in their use
Avoid extending these use mixins instead

the rule now is:

.skin-"shorted proterty"-"value"-"modifier"

e.g. for a light brand background we use: .skin-bg-brand-light

when combining different styles create a unique name e.g.

.skin-box-module {
    background: $color-module;
    @include ds-box-shadow-dark();
}

*/
/**
 * Classes
 */
.sk-bg-white,
.skin-bg-white {
  background: #fff !important;
}

.sk-bg-module,
.skin-bg-module {
  background: #f4f2f0;
}

.sk-bg-module-light,
.skin-bg-module-light {
  background: #faf6f1;
}

.skin-bg-module-dark {
  background: #e9e5e1;
}

.skin-bg-module-lighter {
  background: #fdfcfc;
}

.sk-bg-brand-1,
.skin-bg-brand-1 {
  background: #c70000;
}

.sk-bg-brand-1-dark,
.skin-bg-brand-1-dark {
  background: #910000;
}

.skin-bg-brand-2 {
  background: #946829;
}

.skin-bg-masthead {
  background: #212121;
}

.skin-bg-masthead-light {
  background: #272727;
}

.skin-bg-beige {
  background: #f4eadf;
}

.skin-bg-cream {
  background: #faf6f1;
}

.skin-bg-dark {
  background: #212121;
  color: #fff;
}

.skin-bg-dark a {
  color: #fff;
}

.skin-bg-dark a:hover {
  color: #946829;
}

.skin-bg-dark blockquote {
  color: #fff;
}

@media screen and (min-width: 768px) {
  .skin-bg-dark--desktop {
    background: #212121;
    color: #fff;
  }
  .skin-bg-dark--desktop a {
    color: #fff;
  }
  .skin-bg-dark--desktop a:hover {
    color: #946829;
  }
}

/**
 * Rows
 * Rows of content mostly the widgets
 */
.sk-widget-block,
.skin-widget-block,
.skin-row-module {
  background: #f4f2f0;
}

.skin-row-module-light {
  background: #faf6f1;
}

.skin-row-module-lighter {
  background: #fdfcfc;
}

.skin-row-white-divider,
.skin-row-divider {
  border-top: 1px solid #e9e5e1;
  border-bottom: 1px solid #e9e5e1;
}

.divider,
.skin-divider {
  border-bottom: 1px solid #c9bfb5;
}

/**
 * Skins for boxes
 * Cards and islands
 */
.bg-white,
.skin-box-white {
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.17);
}

.skin-box-pale {
  background-color: #faf6f1;
}

.island-bg-module,
.bg-module,
.skin-box-module {
  background: #f4f2f0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.12);
}

.skin-box-module-border {
  background: #f4f2f0;
  border: 1px solid #ddd5cc;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.12);
}

.skin-box-dark {
  background: #212121;
  color: white;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.12);
}

/**
 * Border styles
