/**
 * Responsive Images
 */
.rs-img {
  display: block;
  width: auto;
  overflow: hidden;
  position: relative;
  padding-bottom: 100%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #946829;
  background-image: url("../img/uol-1-1-tower.png");
}

.rs-img a {
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  transition: background 0.25s ease;
}

.rs-img a:hover {
  background: rgba(0, 0, 0, 0.25);
}

.rs-img img {
  position: absolute;
  top: -9999px;
