blockquote {
    border-left: 4px solid $color-brand-2;
    font-size: 16px;
    color: $color-font;
    line-height: 130%;
    padding: $space-sm;

    @media (min-width: $screen-xs-min) {
      font-family: $font-family-big;
      font-size: 18px;
      padding: $space;
      font-style: normal;
    }

    @media (min-width: $screen-sm-min) {
      font-size: 26px;
    }

    p {
      quotes: "" "" "" "";
      display: inline;
      font-size: inherit;
      line-height: 130%;
      &::before {
          content: open-quote;
      }
      &::after {
          content: close-quote;
      }
    }

    footer {
      @extend %heading-related;

      display: block;
      padding-top: $space-sm;
      margin: 0;
      p::before,
       &::before {
        content: "";
      }
    }
  }

  .blockquote-sm {
    @media (min-width: $screen-sm-min) {
      font-size: 22px;
    }
  }

  .blockquote-no-style {
    border: 0;
    padding: 0;
    margin: 0;
  }
