/*
 * $HEADINGS
 */

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    font-weight: $font-weight-book;
    margin-top: 0;
    margin-bottom: $space-sm;

    @media (min-width: $screen-sm-min) {
        margin-bottom: $space;
    }

    a {
        color: $color-brand-1;

        &:hover {
            color: $color-brand-2;
            text-decoration: underline;
        }
    }

    .no-touch & {

        a {
            text-decoration: none;
        }
    }
}

h1,
.h1 {
    font-size: 30px;

    @media (min-width: $screen-xs-min) {
        font-size: 40px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 45px;
    }

    @media (min-width: $screen-md-min) {
        font-size: 48px;
    }

}

h2,
.h2 {
    font-size: 22px;

    @media (min-width: $screen-xs-min) {
        font-size: 26px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 28px;
    }
}

h3,
.h3 {
    font-size: 20px;

    @media (min-width: $screen-xs-min) {
        font-size: 22px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 24px;
    }
}

h4,
.h4 {
    font-size: 19px;

    @media (min-width: $screen-xs-min) {
        font-size: 20px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 22px;
    }
}

h5,
.h5 {
    font-size: 19px;

    @media (min-width: $screen-xs-min) {
        font-size: 19px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 20px;
    }
}

h6,
.h6 {
    font-size: 19px;

    @media (min-width: $screen-xs-min) {
        font-size: 19px;
    }

    @media (min-width: $screen-sm-min) {
        font-size: 20px;
    }
}

.jadu-cms,
.cms {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        line-height: 130%;

        @media (min-width: $screen-xs-min) {
            line-height: 125%;
        }
    }
}

.cms-heading-serif {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: $font-family-big;
    }
}

.cms-dark {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: white;
    }
}
