.uol-download {
    margin-bottom: 24px;

    @media (min-width: $screen-sm) {
        margin-bottom: 48px;
    }
}

    .uol-download__title {
        font-size: 20px;
        font-weight: 600;

        @media (min-width: $screen-sm) {
            font-size: 32px;
            font-weight: 400;
        }
    }

    .uol-download__items-container {
        padding: 8px;
        background: $color-module;
        border: 1px solid #bcb8b3;
        border-radius: 4px;
    }

    .uol-download__description {
        font-size: 18px;
        font-weight: 600;
    }

    .uol-download__item {
        padding-bottom: 16px;
        margin-bottom: 16px;
        border-bottom: 1px solid $color-brand-2;

        &:last-of-type {
            border-bottom: none;
            padding-bottom: 0;
            margin-bottom: 0;
        }

        @media (min-width: $screen-sm) {
            padding-top: 8px;
            display: flex;
        }
    }

        .uol-download__item__title {
            font-size: 16px;

            @media (min-width: $screen-sm) {
                font-size: 18px;
                width: 50%;
                padding-right: 10px;
            }

            @media (min-width: $screen-md) {
                width: 50%;
            }
        }

        .uol-download__item__meta {
            display: flex;
            font-size: 14px;
            margin-bottom: 8px;

            @media (min-width: $screen-sm) {
                padding-top: 3px;
                width: 30%;
            }
        }
            .uol-download__item__meta__file-type {
                padding-right: 1em;
            }

            .uol-download__item__meta__file-size {

            }

            .uol-download__item__meta__term {
                display: inline;

                &::after {
                    content: ":";
                }
            }

            .uol-download__item__meta__data {
                display: inline;
                text-transform: uppercase;
            }

        .uol-download__item__button-container {

            @media (min-width: $screen-sm) {
                margin-top: -10px;
                width: 30%;

                .uol-button {
                    width: 100%;

                    .ds-icon {
                        float: right;
                    }
                }
            }
        }
