.uol-banner-search {
    background-color: $color-white;
    background-size: 0;

    @media (min-width: 496px) {
        background: #2c2c2c no-repeat 50% / cover;
    }
}

    .uol-banner-search__container {
        margin: 8px;
        padding: 16px 24px 32px;
        border-radius: 4px;
        background: rgba($color-grey-dark, 0.9);
        border: 1px solid $color-white;
        width: 496px;
        max-width: calc(100% - 16px);
        color: $color-white;

        @media (min-width: 496px) {
            margin: 36px 16px 84px;
            max-width: calc(100% - 32px);
        }

        .ds-form-input-container {
            input {
                color: $color-font-dark;
            }
        }
    }

    .uol-banner-search__title {
        font-family: $ds-font-family-serif;
        font-size: $font-size-3;
        margin-bottom: 0.55em;

        @media (min-width: 1024px) {
            font-size: $font-size-2;
        }
    }

    .uol-banner-search__lead {
        font-size: $font-size-5;
        line-height: $line-height-6;
    }
