.uol-info-list-container {
    @extend
        .island,
        .island--featured;

    padding: 16px;
}

.uol-info-list {
    font-size: 16px;
}

    .uol-info-list__group {
        clear: both;
    }

    .uol-info-list__term {
        float: left;
        font-weight: 700;

        &::after {
            content: ":\00a0";
        }
    }

    .uol-info-list__data {
        float: left;

        &::after {
            content: ",\00a0";
        }

        &:last-of-type {
            &::after {
                content: none;
            }
        }
    }
