
/* Breadcrumb decoration */

.uol-breadcrumb-container {
    padding: 0 10px;

    &.full {
        display: block;
        overflow: auto;
    }
}

.uol-breadcrumb {
    position: relative;
    font-size: 13px;
    line-height: 120%;
    margin-bottom: 0;
    padding: 8px 0;
    white-space: nowrap;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    list-style: none;
    overflow: visible;
    display: flex;

    .uol-breadcrumb-container.full & {
        white-space: normal;
        display: block;
        overflow: auto;
    }
}

    .uol-breadcrumb__item {
        display: block;
        white-space: nowrap;

        &::after {
            content: "";
            display: inline-block;
            width: 0.95ex;
            height: 1em;
            position: relative;
            top: 0.25em;
            left: 0.05em;
            margin: 0 0.32em 0 0.04em;
            background: repeating-linear-gradient(
                -75deg,
                rgba(0, 0, 0, 0),
                rgba(0, 0, 0, 0) 4px,
                $color-border--light 5px,
                $color-border--light 5px,
                rgba(0, 0, 0, 0) 6px,
                rgba(0, 0, 0, 0) 10px,
            );
        }

        &:last-of-type {
            color: #212529;
            font-weight: 600;
            overflow: hidden;
            text-overflow: ellipsis;
            
            &::after {
                background: transparent;
            }
        }

        a {
            color: $color-blacklight;

            &:hover {
                text-decoration: none;
                color: $color-brand-2;
                border-bottom: 1px solid $color-brand-2;
            }
        }

        .uol-breadcrumb-container.full & {
            white-space: normal;
            display: inline-block;
        }
    }

    .uol-breadcrumb__more {
        background: #e9e5e1;
        color: #212529;
        padding: 0 10px;
        margin: 6px 8px 0 0;
        color: #000;
        border-radius: 3px;
        vertical-align: top;
        border: 0;
        left: 0;
        overflow: hidden;
        float: left;
        font-size: 14px;
    }
