.uol-cards {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1rem;
    padding: 0;
}

.uol-card-container {
    display: flex;
    padding: 0 1rem;
    width: 100%;

    .uol-cards--count-2 &,
    .uol-cards--count-4 & {
        @media (min-width: 768px) {
            width: 50%;
        }
    }

    .uol-cards--count-3 &,
    .uol-cards--count-6 & {

        @media (min-width: 768px) {
            width: 33.3333%;
        }
    }

    .uol-cards--count-5 & {

        @media (min-width: 768px) {
            width: 33.3333%;
        }

        &:nth-child(-n+2) {
            @media (min-width: 768px) {
                width: 50%;
            }
        }
    }
}
