.uol-search-combo {
    display: flex;
    padding-top: 3rem;

    .ds-form-input-container {
        position: relative;
        width: calc(100% - 50px);
        font-size: 18px;

        label {
            position: absolute;
            top: -3rem;
        }

        input {
            margin-bottom: 0;
            padding: 13px 16px;
            border: 1px solid $color-border-dark;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            height: 48px;

            &::placeholder {
                font-size: 18px;
            }

            &:focus {
                border-radius: inherit;
                outline-offset: -1px;
            }
        }
    }

    .uol-button[type=submit] {
        width: 48px;
        height: 48px;
        padding: 0;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;

        &:focus {
            border-radius: inherit;
            outline-offset: -1px;
        }
    }

    .uol-input-clear {
        border: none;
        padding: 12px 12px 11px;
        bottom: 1px;
    }
}
