a {
    color: $color-link;

    &:hover {
      color: $color-brand-1--dark;
      text-decoration: underline;
    }
  }

  %inline-link { // General inline link styles
      color: $color-link;
    text-decoration: none !important;
    border-bottom: 1px solid $color-border;
    line-height: 100%;

    &:hover {
      color: $color-brand-2;
      border-bottom: 1px solid $color-brand-2;
    }
  }

  %hover-border {
    text-decoration: none;
    border-bottom: 1px solid rgba($color-border, 0);
    transition: border-bottom 0.25s ease, color 0.25s ease;
    &:hover {
      text-decoration: none;
      border-bottom: 1px solid $color-border;
    }
    }

    //Missing chars should be a thinner weight
    .missing-char {
        font-weight: normal;
        font-family: Helvetica, sans-serif;
    }

  .jadu-cms,
  .cms {

    p {
      overflow-wrap: break-word;
      word-wrap: break-word;
    }

    a {
      overflow-wrap: break-word;
      word-wrap: break-word;
    }

      a > img {
      border: 1px solid $color-border;
      padding: $space-half/2;

      &:hover {
        border: 1px solid $color-border;
      }
    }

      hr {
           clear: both;
        }
  }

  /**
   * Buttons
   */

  .cms {
    .btn {
      color: white;
      &:hover {
        //border: 0;
      }
    }
  }

  .cms-dark {
    a {
      color: $color-white;
      border-bottom: 1px solid $color-white;
      &:hover {
        text-decoration: none;
        border-bottom: 1px solid $color-brand-2;
      }
    }
  }

  /**
   * More
   * Fix courses more in cms link
   */

  %more {
    @extend %all-caps;
    @extend %hover-border;

    display: inline-block;
    color: $color-font;

    &::after {
      @include ds-icon();
      @include ds-icon-chevron-right();

      margin-left: 5px;
      position: relative;
      color: $color-brand-2;
    }

    &:focus {
      color: $color-brand-2;
      text-decoration: none;
    }
  }

  .more,
  .cms .more { //All caps with black arrow
    @extend %more;
  }

  %more-dark,
  .more-dark,
  .cms .more-dark { //All caps with black arrow
    @extend %more;

    color: $color-font;
  }

  .more-light,
  .cms .more-light { //white text white arrow
    @extend %more;

    color: $color-white;
    &::after {
      color: $color-white;
    }
  }

    .more-divider,
    .cms .more-divider {
        //text-align: left;
        border-top: 1px solid $color-brand-2;
        display: block;
        padding: $space-half 0;
        border-bottom: 0;
        &:hover {
            border-bottom: 0;
        }
        &.color-white,
        &.more-light {
            border-top: 1px solid $color-white;
            color: $color-white;
            &::after {
                color: white;
            }
        }
    }

    .more-expand,
    .cms .more-expand {
      &::after {
        @include ds-icon-chevron-down();
      }

      &.active::after {
        @include ds-icon-chevron-up();
      }
  }

  .more--v2 {
        color: $color-font;
        text-transform: uppercase;
        font-weight: normal;

        @media (max-width: 767px) {
            font-weight: bold;
            color: black;
            display: inline-block;
            width: auto;
            min-width: 115px;
            border: 1px solid $color-brand-2;
            position: absolute;
            bottom: -3.5em;
            left: 62px;
            right: 62px;
            // transform: translateX(-50%);
            text-align: center;

            &::after {
              content: none;
            }

            a {
                font-size: 14px;
                color: inherit;
                font-weight: normal;
                display: block;
                padding: 8px;
            }

            &:hover {
                background: $color-brand-2;
                a {
                    color: #fff;
                    text-decoration: none;
                }
            }
          }

          @media (min-width: $screen-sm-min) {
            position: absolute;
            bottom: initial;
            top: -38px;
            right: 10px;

            a {
                color: #151414;

                &:hover {
                    text-decoration: none;
                }
            }
          }
    }

  /**
   * Other
   */

  .filter { //All caps with black arrow
    @extend %all-caps;

    display: inline-block;
    color: $color-brand-1;
    text-decoration: none;
    text-align: center;
    background: none;
    border: 0;
    margin: 0;

    &:hover {
      text-decoration: none;
    }

    &::after {
      @include ds-icon;
      @include ds-icon-chevron-down();

      margin-left: 5px;
      position: relative;
      top: 2px;
      color: $color-brand-2;
    }

    &.active::after {
      @include ds-icon();
      @include ds-icon-close();

      margin-left: 5px;
      position: relative;
      top: 2px;
      color: $color-brand-2;
    }
  }

  /**
   * Link wrap - Not a styled link, just used for
   */

  .link-wrap { // parent should be position: relative
    font-size: 0;
    text-indent: -9999px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  .link-color-1 {
    color: $color-brand-1;

    &:hover,
    &:focus {
      color: $color-brand-2;
    }
  }

  .link-color-2 {
    color: $color-brand-2;

    &:hover,
    &:focus {
      color: $color-brand-1;
    }
  }
