/**
 * Sidebar
 */

$side-pad-sm: 7px;
$side-pad-md: 8px;
$side-pad-lg: 9px;

$side-size-sm: 15px;
$side-size: 16px;
$sidebar-breakpoint: $wrapper-lg;

.sidebar-heading {
	padding: $side-pad-sm $side-pad-sm*2;
	margin: 0;
	margin-bottom: 0;
	border-bottom: 1px solid $color-border;
	display: none;

	@media (min-width: $screen-md-min) {
		margin-bottom: 0;
		display: block;
		padding: $side-pad-md;
	}

	@media (min-width: $sidebar-breakpoint) {
		padding: $side-pad-lg $side-pad-lg*2 $side-pad-lg 0;
	}

}

/**
 * Mobile open menu button
 */

.sidebar-button {
	@extend %all-caps;

	display: block;
	width: 100%;
	background: $color-module;
	padding: 9px 30px 9px 30px;
	border: 0;
	//border-left: 4px solid $color-brand-2;
	text-align: left;
	position: relative;

	@media (min-width: $screen-xs - 1) {
		font-size: $side-size-sm;
		padding: 10px 5px 10px 40px;
	}

	@media (min-width: $screen-md-min) {
		display: none;
	}

	&:before {
		@include ds-icon();
		@include ds-icon-navicon();
		color: $color-brand-2;
		position: absolute;
		top: 50%;
		left: 10px;
		margin-top: -8px;
		font-size: $side-size;
		@media (min-width: $screen-xs-min) {
			left: 15px;
		}
	}

	&:after {
		@include ds-icon();
		@include ds-icon-chevron-down();
		position: absolute;
		top: 50%;
		right: 10px;
		margin-top: -8px;
		font-size: $side-size;
		@media (min-width: $screen-xs-min) {
			right: 15px;
		}
	}

	.state-sidebar-active & {
		background: $color-brand-2;
		color: white;
		&::before {
			@extend .ds-icon-close::before;

			color: white;
		}
		&::after {
			@extend .ds-icon-chevron-up::before;

			color: white;
		}
	}
}

.sidebar-body, //deprecated
.sidebar-container {
	display: none;

	@media (min-width: $screen-md-min) {
		display: block;
	}
	.state-sidebar-active & {
		@media (max-width: $screen-md-max) {
			display: block;
		}
	}
}

.sidebar-body-fw, //deprecated
.sidebar-container-fw { //full width sidebar body modifier
	.sidebar-heading {
		@extend %heading-related !optional;
		margin: 0;
		padding-left: $space-half;
		color: $color-font;
	}
}

.sidebar-nav,//old class name
.sidebar-nav-fw { //place on ul

	margin: 0 0 $space 0;
	padding: 0;

	ul {
		display: none;
		padding: 0;
		margin: 0;
		border-left: 2px solid $color-brand-2;
		background: rgba(#4d4843, 0.04);
	}

	li {
		list-style: none;
		z-index: 1;
	}

	a {
		font-size: $side-size;
		padding: $side-pad-sm $side-pad-sm*2;
		display: block;
		color: $color-font;
		border-bottom: 1px $color-border solid;
    	transition: all 0.25s ease;

		@media (min-width: $screen-md-min) {
			padding: $side-pad-md $side-pad-md;
		}

		@media (min-width: $screen-lg-min) {
			padding: $side-pad-lg;
		}

		&:hover {
			text-decoration: none;
			background: $color-module;
		}

		&:focus {
			text-decoration: none;
		}
	}

	> li > a { //top level
		@media (min-width: $sidebar-breakpoint) {
			padding: $side-pad-lg $side-pad-lg*2 $side-pad-lg 0;
		}
    }
    
    > li > a {
		padding-left: $space-half;
	}

	.dropdown > a {
		position: relative;
		padding-right: 26px;

		&:after {
			@include ds-icon();
			@include ds-icon-chevron-down();
			right: 5px;
			position: absolute;
			top: 50%;
			margin-top: -7px;
			color: $color-brand-2;
			transition: transform 0.25s ease;
		}
	}

	.dropdown.open > ul {
		display: block;
	}

	.dropdown.open > a {
		position: relative;
		padding-right: 26px;

		&:after {
			transform: rotate(180deg);
			//@include ds-icon-chevron-up();
		}
	}

	.active > a {
		background: white;
	}
}

.no-js ul.sidebar-nav {
	.dropdown > ul {
		display: block;
	}
}


/**
 * Sidebar Actions
 */

%sidebar-list { //extendable style

	padding: 0;
	margin: 0 0 $space-xs 0;

	@media (min-width: $screen-md-min) {
		margin-bottom: $space-lg;
	}

	li {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	a {
		padding: $side-pad-sm 20px $side-pad-sm $side-pad-sm;
		display: block;
		position: relative;
		font-size: $side-size;

		@media (min-width: $screen-lg-min) {
			padding: $side-pad-md 20px $side-pad-md $side-pad-md;
		}

		&:hover {
			text-decoration: none;
		}

		&:before {
			@extend .ds-icon;
			@extend .ds-icon-chevron-right::before;
			position: absolute;
			top: 50%;
			margin-top: -8px;
			right: 6px;
			color: $color-brand-2;
		}
	}

	[class*="ds-icon"] {
		position: relative;
		margin-right: 6px;
	}
}

//Plain sidebar list
.sidebar-list {
	@extend %sidebar-list;
	a {
		border-bottom: 1px solid $color-border;
		color: $color-font;
		padding: $side-pad-sm;

		@media (min-width: $screen-lg-min) {
			padding: $side-pad-md;
		}

		&:before {
			display: none;
		}
	}
}

.sidebar-university, //deprecated
.sidebar-contact, //deprecated
.sidebar-cta-list {	// plain list

	@extend %sidebar-list;

	a {
		border-bottom: 1px solid $color-border;
		color: $color-font;
		z-index: 1;
	}

	[class*="ds-icon"] {
		color: $color-brand-2;
	}
}

.sidebar-cta, //depracted
.sidebar-cta-btn {	//gold boxes
	@extend %sidebar-list;

	a {
		@extend .all-caps;
		color: white;
		background: $color-brand-2;
		border-bottom: 1px solid white;
		font-size: $side-size-sm;
		transition: all .3s ease;
		z-index: 1;

		&:before {
			color: white;
		}

		&:hover {
			background: $color-font-dark;
		}
	}

}

