.local-header-inner {
    display: table;
    width: 100%;
    a {
        color: white;
    }
}

/**
 * The site title
 */

.local-header-title {
    display: table-cell;
    vertical-align: middle;
    padding: 10px 40px 10px 0;

    > a {
        font-size: 18px;

        margin: 0;
        font-family: $font-family-sans-serif;

        @media (min-width: $screen-xs-min) {
            font-size: 18px;
        }

        @media (min-width: $screen-sm-min) {
            font-size: 20px;
        }

        .ds-icon {
            vertical-align: middle;
        }
    }

    a {
        border: 0;
        color: white;
        &:hover {
            color: white;
        }
    }
}

    .local-header-title__heading {
        font-size: 18px;
        margin-bottom: 0;
        
        @media (min-width: $screen-sm-min) {
            font-size: 20px;
        }
    }

/**
 * The local header call-to-ation button
 * As seen on library
 */

.local-header-cta {
    display: table-cell;
    text-align: right;
    vertical-align: middle;
    height: 100%;

    a {
        background: $color-brand-1--dark;
        padding: 10px;
        text-transform: uppercase;
        font-size: 15px;
        display: inline-block;
        transition: background 0.25s ease;

        @media (min-width: $screen-sm-min) {
            font-size: 16px;
        }

        &:hover {
            background: $color-brand-1;
            text-decoration: none;
        }
    }
}

/**
 * The local header nav 
 * As seen on courses/oci
 */

.local-header-nav {
    display: table-cell;
    text-align: right;
    vertical-align: middle;
    height: 100%;

    ul {
        margin: 0;
        padding: 0;
        overflow: hidden;
        float: right;
    }

    li {
        float: left;
        list-style: none;
    }

    li.active a {
        background: $color-brand-1--dark;
    }

    a {
        display: block;
        background: $color-brand-1;
        padding: 13px;
        text-transform: uppercase;
        font-size: 15px;
        transition: background 0.25s ease;

        @media (min-width: $screen-sm-min) {
            font-size: 16px;
        }

        &:hover {
            background: $color-brand-1--dark;
            color: $color-white;
            text-decoration: none;
        }
    }
}
