.ds-card {
    position: relative;
    margin: 0 0 16px;
    box-shadow: rgba(0, 0, 0, 0.17) 0 1px 2px 0;
    border-bottom: 5px solid $color-brand-2;
    width: 100%;
    margin-bottom: 12px;
    padding: 16px;
}

    .ds-card--image {
        padding-top: 50%;
    }

    .ds-card--stacked {
        @media (min-width: $screen-xs) {
            position: relative;
            overflow: auto;
            padding: 16px;
        }
    }

    .ds-card__img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        padding-bottom: 50%;
        background: $color-brand-2 50% / cover;
        overflow: hidden;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: inherit;
            transition: all 0.3s ease;

            .ds-card:hover &,
            .ds-card:focus-within & {
                transform: scale(1.05);
            }
        }

        .ds-card--stacked & {

            @media (min-width: $screen-xs) {
                position: relative;
                width: 33.33%;
                float: left;
                padding-bottom: 16%;
            }
        }
    }

    .ds-card__content {

        .ds-card--stacked & {
            padding: 0;
            margin: 0;
        }
    }

    .ds-card__text-container {

        .ds-card--image & {
            padding-top: 16px;
        }

        @media (min-width: $screen-xs) {
            .ds-card--stacked.ds-card--image & {
                float: right;
                width: 66.6%;
                padding: 0 0 0 16px;
            }
        }
    }

    .ds-card__heading {
        font-family: freight-display-pro, sans-serif;
        font-size: 20px;
        line-height: 130%;
    }

        .ds-card__heading__link {
            color: $color-font;

            &::before {
                content: "";
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: 2;
            }

            .ds-card:hover &,
            .ds-card:focus & {
                color: $color-brand-2;
            }
        }

    .ds-card__date {
        font-family: freight-sans-pro, sans-serif;
        margin: 0;
        color: $color-font-light;
        margin-bottom: 8px;
        line-height: 140%;
        text-transform: uppercase;
        font-size: 14px;
    }

    .ds-card__summary {
        color: $color-font-light;
        line-height: 150%;
        font-size: 14px;
    }
