.uol-announcements {

}

.uol-announcement {
    position: relative;
    background-color: $color-white;

    @include media(">desktop-lg") {
        padding: 8px 16px;
    }

    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 1px;
        background: $color-border--light;
    }

    @include media("<desktop-lg") {

        &.uol-announcement--info::after {
            background: linear-gradient(90deg, $color-alert--info 0, $color-alert--info 8px, $color-border--light 8px);
        }

        &.uol-announcement--error::after {
            background: linear-gradient(90deg, $color-alert--error 0, $color-alert--error 8px, $color-border--light 8px);
        }

        &.uol-announcement--success::after {
            background: linear-gradient(90deg, $color-alert--success 0, $color-alert--success 8px, $color-border--light 8px);
        }

        &.uol-announcement--warning::after {
            background: linear-gradient(90deg, $color-alert--warning 0, $color-alert--warning 8px, $color-border--light 8px);
        }
    }
}

    .uol-announcement--dismissible {
        .js & {
            display: none;
        }
    }

    .uol-announcement__dismiss {
        position: absolute;
        background: none;
        border: none;
        font-size: 14px;
        padding: 8px;
        top: 0px;
        right: 4px;

        @include media(">=tablet") {
            top: 2px;
            font-size: 16px;
            right: 8px;
        }

        @include media(">=desktop-lg") {
            top: 8px;
            right: 16px;
        }
    }

    .uol-announcement__inner {
        max-width: 1400px;
        margin: auto;
        padding: 4px 16px;
        border-left: 8px solid;

        .uol-announcement--info & {
            border-left-color: $color-alert--info;
        }

        .uol-announcement--error & {
            border-left-color: $color-alert--error;
        }

        .uol-announcement--success & {
            border-left-color: $color-alert--success;
        }

        .uol-announcement--warning & {
            border-left-color: $color-alert--warning;
        }
    }

    .uol-announcement__title {
        line-height: 1.2em;
        font-size: 16px;
        font-weight: $font-weight-bold;
        color: $color-font--dark;
        margin-bottom: 0;

        .uol-announcement--dismissible & {
            padding-right: 24px;
        }

        @include media(">=tablet") {
            font-size: 20px;
        }

        @include media(">=desktop") {
            max-width: calc(100% / 3 * 2);
        }
    }

        .uol-announcement__title__button {
            text-align: inherit;
            background: none;
            border: none;
            padding: 0;

            &:hover {
                text-decoration: underline;
            }
        }

    .uol-announcement__content {
        .js & {
            display: none;
        }
    }

    .uol-announcement__text {
        padding-top: 8px;

        @include media(">=desktop") {
            max-width: calc(100% / 3 * 2 - 32px);
        }

        p {
            font-size: 14px;
            line-height: 1.5em;

            @include media(">=tablet") {
                font-size: 16px;
            }

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    .uol-announcement__link {
        display: inline-block;
        color: $color-font;
        text-decoration: underline;
        font-size: 14px;
        font-weight: $font-weight-bold;
        margin-top: 8px;

        @include media(">=tablet") {
            font-size: 16px;
        }
    }
