.uol-form-select-chained {

}

    .uol-form-select-chained--inline {

        @media (min-width: $screen-sm) {
            display: flex;
            justify-content: space-between;
            column-gap: 20px;

            > * {
                width: calc(50% - 10px)
            }
        }
    }

    .uol-form-select-chained__child {

    }

        .uol-form-select-chained__child--disabled {
            .js & {
                @extend .sr-only;
            }
        }