/**
 * More
 * Fix courses more in cms link
 */

@import "../../typography/ds-icons/ds-icons-mixins";

%more {
    @extend %all-caps;
    @extend %hover-border;
    display: inline-block;
    color: $color-font;

    &:after {
        @include ds-icon();
        @include ds-icon-chevron-right();
        margin-left: 5px;
        position: relative;
        color: $color-brand-2;
    }

    &:focus {
        color: $color-brand-2;
        text-decoration: none;
    }
}

.more,
.cms .more { //All caps with black arrow
    @extend %more;
}

%more-dark,
.more-dark,
.cms .more-dark { //All caps with black arrow
    @extend %more;

    color: $color-font;
}

.more-light,
.cms .more-light { //white text white arrow
    @extend %more;

    color: $color-white;
    &:after {
        color: $color-white;
    }
}

.more-divider,
.cms .more-divider {
    //text-align: left;
    border-top: 1px solid $color-brand-2;
    display: block;
    padding: $space-half 0;
    border-bottom: 0;

    &:hover {
        border-bottom: 0;
    }
    &.color-white,
    &.more-light {
        border-top: 1px solid $color-white;
        color: $color-white;
        &::after {
            color: white;
        }
    }
}

.more-expand,
.cms .more-expand {
    &::after {
        // @include ds-icon-chevron-down();
    }

    &.active::after {
        // @include ds-icon-chevron-up();
    }
}

.more--v2 {
    color: $color-font;
    text-transform: uppercase;
    font-weight: normal;

    @media (max-width: 767px) {
        font-weight: bold;
        color: black;
        display: inline-block;
        width: auto;
        min-width: 115px;;
        border: 1px solid $color-brand-2;
        position: absolute;
        bottom: -3.5em;
        left: 62px;
        right: 62px;
        // transform: translateX(-50%);
        text-align: center;

        &::after {
            content: none;
        }

        a {
            font-size: 14px;
            color: inherit;
            font-weight: normal;
            display: block;
            padding: 8px;
        }

        &:hover {
            background: $color-brand-2;
            a {
                color: #fff;
                text-decoration: none;
            }
        }
        }

        @media (min-width: $screen-sm-min) {
        position: absolute;
        bottom: initial;
        top: -38px;
        right: 10px;

        a {
            color: #151414;

            &:hover {
                text-decoration: none;
            }
        }
    }
}
