.site-footer-upper {
    padding: 5px 0;
    border-top: 1px solid $color-border-light;
    background: $color-white;

    body.business & {
        background: $color-masthead;
    }
}

    .site-footer-upper--dark {
        background: $color-masthead-dark;
    }

    .site-footer-upper-social, //deprecated
    .footer-social {

        //outline: 1px solid blue;
        width: 100%;
        display: flex;
        flex-wrap: wrap;

        align-items: center;
        justify-content: center;

        @media (min-width: $screen-sm-min) {
            max-width: 50%;
            width: auto;
            float: right;
        }

        & > a {
            @include ds-transition();
             color: $color-brand-2;
             text-align: center;
             margin: $space-half $space-half;

             @media (min-width: $screen-sm-min) {
                 margin: $space 0 $space $space;
             }

             @media (min-width: $screen-md-min) {
                 margin: 14px 0 14px $space;
             }

             &:hover {
                 color: $color-font;
                 text-decoration: none;
             }
             
             .site-footer-upper-dark & {
                &:hover,
                &:focus {
                    color: lighten($color-brand-2, 30%);
                } 
             }
         }

         .ds-icon {
             font-size: 24px;
             line-height: 1;

             @media (min-width: $screen-md-min) {
                 font-size: 32px;
             }
         }
    }

    .site-footer-upper-logos, //deprecated
    .footer-img {
        display: flex;
        align-items: center;
        justify-content: center;

        @media (min-width: $screen-sm-min) {
            max-width: 50%;
            width: auto;
            float: left;
        }

        & > a {
             text-align: center;
             &:hover {
                 text-decoration: none;
             }
        }

        img {
            margin: 0 $space-half $space-half 0;
            max-height: 55px;

             @media (min-width: $screen-sm-min) {
                 margin: 0 $space-half 0 0;
             }

            @media (min-width: $screen-md-min) {
                max-height: 60px;
            }
        }
    }

.site-footer-upper-dark {
    background: $color-masthead-light;
    border-top: 0;
}